import React from 'react';
import { Row, Col, Image } from 'react-bootstrap';

class RightText extends React.Component {
  render() {
    return (
      <Row className={this.props.className + ' blue-lines-right'}>
        <Col lg={{ span: 6 }}>
          <Row className="p-0 middle-blue bg-primary">
            <div className="offset-11 col middle-white bg-white h-100" />
          </Row>
          <Row className="p-0">
            <Image
              className="full-img left-img lazyload"
              src={this.props.headImage}
            />
          </Row>
          <Row className="full-blue bg-primary">
            <div className="col-6 p-0 m-0">
              <Image
                className="close-img lazyload"
                src={this.props.footerImage}
              />
            </div>
            <div className="col-5 d-flex align-items-center justify-content-center">
              <Image
                className="w-25 lazyload"
                src="assets/images/white-icon.png"
              />
            </div>
            <div className="col bg-white h-100" />
          </Row>
        </Col>
        <Col
          className="col-12 text-center text-lg-left"
          lg={{ span: 4, offset: 1 }}
        >
          <div className="pl-4 pr-4 pl-lg-0 pr-lg-0">
            <h1 className="title mt-5 mb-4 text-uppercase">
              {this.props.title}
            </h1>
            <p className="description pb-5">{this.props.description}</p>
          </div>
        </Col>
      </Row>
    );
  }
}

export default RightText;
