const convertEvent = (args) => {
  if (typeof window !== 'undefined') {
    if (window.gtag != null) {
      window.gtag('event', 'conversion', args);
    }
  }
};
const gtag_report_conversion = (id, url) => {
  var callback = function () {
    if (typeof url != 'undefined') {
      window.location = url;
    }
  };

  convertEvent({
    send_to: id,
    event_callback: callback,
  });
  return false;
};

export { gtag_report_conversion, convertEvent };
