import './about.scss';
import React from 'react';
import { Row, Col, Container, Image, Card, Button } from 'react-bootstrap';
import {
  AppBar,
  LeftText,
  RightText,
  BottomBar,
  ContactInfo,
  BottomBarResponsive,
} from '../components';
class AboutScreen extends React.Component {
  render() {
    return (
      <Container fluid className="pl-0">
        <AppBar white />
        <LeftText
          isVideo
          className="mt-5 pt-3"
          title="Nosotros"
          description="¡Nacemos para unir a las MIPYMES del país con las personas!"
          headImage="https://www.youtube.com/embed/7C7OOmtAerA"
          footerImage="assets/images/about1.jpg"
        />
        <RightText
          title="Ningún virus tendrá tanta fuerza como nuestros sueños"
          description="PYPPER nació pensando en Juan, María, Sandra, Pedro, Claudia y todas las miles de MIPYMES que hay en nuestro país y que hoy necesitan dar un paso a las plataformas digitales, pero no cuentan con el presupuesto y conocimiento para hacerlo."
          headImage="assets/images/about2.jpg"
          footerImage="assets/images/about3.jpg"
        />
        <LeftText
          title="Nuestra misión es conectar a las MIPYMES con el mundo digital."
          description="Hoy más que nunca, la solidaridad se convierte en nuestra mejor defensa y herramienta de trabajo, es por eso que nace este proyecto."
          headImage="assets/images/about4.jpg"
          footerImage="assets/images/about5.jpg"
        />
        <RightText
          title="NUESTRO OBJETIVO"
          description="Cerrar la brecha entre el mundo digital y las MIPYMES de Colombia."
          headImage="assets/images/about6.jpg"
          footerImage="assets/images/about7.jpg"
        />
        <LeftText
          title="NUESTRA VISIÓN"
          description="Ser la plataforma digital que une a las MIPYMES con las personas."
          headImage="assets/images/about8.jpg"
          footerImage="assets/images/about1.jpg"
        />
        <RightText
          title="¿cómo lo hacemos?"
          description="PYPPER es una comunidad de MIPYMES que se relacionan con sus clientes y entre sí. Nos encargamos de dar la visibilidad necesaria para que sus productos sean conocidos por sus clientes potenciales."
          headImage="assets/images/about6.jpg"
          footerImage="assets/images/about5.jpg"
        />
        <LeftText
          title="¡CREAMOS RELACIONES DE VALOR!"
          description="Ponemos en contacto a las MIPYMES entre sí para que juntas puedan intercambiar sus servicios creciendo juntos."
          headImage="assets/images/about9.jpg"
          footerImage="assets/images/about3.jpg"
        />
        <ContactInfo />
        <BottomBar unfixed center />
        <BottomBarResponsive />
      </Container>
    );
  }
}

export default AboutScreen;
