import './company-list.scss';
import React from 'react';
import {
  Container,
  Row,
  Form,
  Col,
  Card,
  Image,
  Button,
  Spinner,
  Carousel,
} from 'react-bootstrap';
import queryString from 'query-string';
import {
  AppBar,
  SearchCard,
  ContactInfo,
  BottomBar,
  MapContainer,
  BottomBarResponsive,
} from '../components';
import { CompanyRepository } from '../repositories';
import { Link } from 'react-router-dom';
import { trackEvent } from '../common/api/facebook-pixel';
import { AuthModel } from '../models';
import { GeocodeAPI } from '../common/api';

class CompanyListScreen extends React.Component {
  constructor(props) {
    super(props);
    const query = queryString.parse(this.props.location.search);
    this.state = {
      filterText: query ? query.filterText : '',
      companies: [],
      loading: true,
      type: undefined,
      locations: [],
    };
  }
  componentDidMount() {
    this.fetchCompanies(this.state.filterText);
  }
  render() {
    return (
      <Container fluid>
        <AppBar white />
        <Row className="w-100 search-form bg-primary mt-5 pt-3 d-lg-none">
          <div className="col-12">
            <Form>
              <Row>
                <Form.Group controlId="formFilter" className="col-6">
                  <Form.Text className="text-white">¿Qué buscas?</Form.Text>
                  <Form.Control
                    type="text"
                    className="bg-primary text-white"
                    ref={(c) => (this.filter = c)}
                  />
                </Form.Group>
                <Form.Group controlId="formLocation" className="col-6">
                  <Button
                    variant="light"
                    className="w-100 mt-4"
                    onClick={() => this.fetchCompanies(this.filter.value)}
                  >
                    Buscar
                  </Button>
                </Form.Group>
              </Row>
            </Form>
          </div>
        </Row>

        <Row className="mt-lg-5 pt-lg-3">
          <div className="d-none d-lg-block col-3 bg-primary white-lines pt-3 pl-5">
            <h4 className="title-white text-white">MAPA</h4>
            <div className="map">
              <MapContainer locations={this.state.locations} />
            </div>
            {this.buildCategories()}
            {AuthModel.isAuthenticated ? (
              <div className="text-center mt-5">
                <Button variant="outline-light">CERRAR SESIÓN</Button>
              </div>
            ) : (
              <div className="text-center mt-5">
                <Button variant="outline-light">INICIAR SESIÓN</Button>
              </div>
            )}
          </div>
          <div className="col-12 col-lg-9">
            <Form className="col-8 d-none d-lg-block search-form">
              <Row>
                <Form.Group
                  controlId="formFilter"
                  className="col-3 offset-2 p-0"
                >
                  <Form.Control
                    type="text"
                    placeholder="¿Qué buscas?"
                    ref={(c) => (this.filter2 = c)}
                  />
                </Form.Group>
                <Form.Group controlId="formLocation" className="col-3 p-0">
                  <Form.Control
                    type="text"
                    placeholder="¿Dónde estás?"
                    ref={(c) => (this.location2 = c)}
                  />
                </Form.Group>
                <div className="col-2 pl-1 mt-1">
                  <Button
                    variant="primary"
                    className="w-100"
                    onClick={() => this.fetchCompanies(this.filter2.value)}
                  >
                    Buscar
                  </Button>
                </div>
              </Row>
            </Form>
            <div className="mt-5 pt-2 pt-lg-0 mt-lg-0"></div>
            <Row className="mt-5 pt-4 pt-lg-0">
              {this.state.loading ? (
                <div className="col-12 d-flex justify-content-center mt-5">
                  <Spinner
                    className={{
                      'd-none': !this.state.loading,
                      'd-block': this.state.loading,
                    }}
                    as="span"
                    animation="border"
                    size="md"
                    role="status"
                    aria-hidden="false"
                  />
                </div>
              ) : (
                <>
                  <div className="map-responsive d-lg-none col-12 p-0">
                    <MapContainer
                      isResponsive
                      locations={this.state.locations}
                    />
                  </div>
                  {this.buildCompanies()}
                  {this.buildAdds()}
                </>
              )}
            </Row>
          </div>
        </Row>
        <Row>
          <div className="d-none d-lg-block col-3 bg-primary pt-3 pl-5"></div>
          <div className="col-12 col-lg-9">{this.buildHowWeDo()}</div>
        </Row>
        <ContactInfo />
        <BottomBar unfixed />
        <BottomBarResponsive />
      </Container>
    );
  }
  buildCompanies() {
    if (this.state.companies.length == 0) {
      return (
        <div className="text-center w-100 font-weight-bold text-primary text-uppercase">
          <div className="col-10 offset-1 col-lg-6 offset-lg-3">
            <Image
              className="w-100 lazyload"
              src="assets/images/no-companies.png"
            />
          </div>
          <p className="mt-5">Estamos construyendo la comunidad PYPPER,</p>
          <p>¡pronto encontrarás lo que estás buscando</p>
        </div>
      );
    }
    return this.state.companies.map((company) => this.buildCompany(company));
  }
  buildCompany(company) {
    if (company.description.length > 150) {
      company.description = company.description.substr(0, 150) + '...';
    }
    return (
      <div className="mt-5 col-12 col-md-6 pl-5 pr-5 pb-4 list-card">
        <Card className="p-0 h-100">
          <Card.Body className="p-0 h-100">
            <Row className="h-100">
              <div className="col-12 col-lg-5">
                <Image
                  className="w-100 h-100"
                  src={company.profilePictureLink}
                />
              </div>
              <div className="col-12 col-lg-7 mt-3 mb-3">
                <h3 className="bg-primary text-white font-weight-bold pb-2 pt-2 text-center">
                  {company.name}
                </h3>
                <p className="list-item-description pl-3 pr-3 pl-lg-0">
                  {company.description}
                </p>
                <Col className="text-center">
                  <Link to={`company/${company.id}`}>
                    <Button
                      variant={company.isActive ? 'primary' : 'danger'}
                      onClick={() => trackEvent('Contact')}
                    >
                      Visitar
                    </Button>
                  </Link>
                </Col>
              </div>
            </Row>
          </Card.Body>
        </Card>
      </div>
    );
  }
  buildAdds() {
    if (this.state.companies.length > 0) {
      const companiesWithOffers = this.state.companies.filter((company) => {
        return company.offerLink != undefined;
      });
      return (
        <div className="col-12">
          <Row>
            <div className="col-12 col-lg-6 bg-primary text-right text-white pt-2 pb-1">
              <h4>OFERTAS RECOMENDADAS</h4>
            </div>

            <div className="col-lg-4 offset-lg-4 mt-5 d-lg-none">
              <Carousel className="offer-image">
                {companiesWithOffers.map((company) =>
                  this.buildCarouselItem(company)
                )}
              </Carousel>
            </div>

            <div className="d-none d-lg-block col-12 mt-5">
              <Row>
                {companiesWithOffers[1] ? (
                  <div className="col-4">
                    <Link to={`/company/${companiesWithOffers[1].id}`}>
                      <Image
                        className="offer-image lazyload"
                        src={
                          companiesWithOffers[1]
                            ? companiesWithOffers[1].offerLink
                            : ''
                        }
                      />
                    </Link>
                  </div>
                ) : null}
                {companiesWithOffers[0] ? (
                  <div className="col-4">
                    <Link to={`/company/${companiesWithOffers[0].id}`}>
                      <Image
                        className="offer-image lazyload"
                        src={
                          companiesWithOffers[0]
                            ? companiesWithOffers[0].offerLink
                            : ''
                        }
                      />
                    </Link>
                  </div>
                ) : null}

                {companiesWithOffers[2] ? (
                  <div className="col-4">
                    <Link to={`/company/${companiesWithOffers[2].id}`}>
                      <Image
                        className="offer-image lazyload"
                        src={
                          companiesWithOffers[2]
                            ? companiesWithOffers[2].offerLink
                            : ''
                        }
                      />
                    </Link>
                  </div>
                ) : null}
              </Row>
            </div>
          </Row>
        </div>
      );
    }
  }

  buildCarouselItem(company) {
    return (
      <Carousel.Item className="offer-image">
        <Image className="offer-image lazyload" src={company.offerLink} />
      </Carousel.Item>
    );
  }
  buildHowWeDo() {
    return (
      <div className="col-12 text-center mt-5">
        <h3 className="text-primary">¿CÓMO LO HACEMOS?</h3>

        <Row>
          <div className="mt-5 col-12 col-md-4 pl-5 pr-5 pb-4">
            <Card className="p-0">
              <Card.Body className="pl-4 pr-4">
                <Image
                  className="w-75 mb-3 lazyload"
                  src="assets/images/how1.png"
                />
                <h3 className="text-primary font-weight-bold">1</h3>
                <hr />
                <h5 className="font-weight-bold">REGÍSTRATE</h5>
              </Card.Body>
            </Card>
          </div>
          <div className="mt-5 col-12 col-md-4 pl-5 pr-5 pb-4">
            <Card className="p-0">
              <Card.Body className="pl-4 pr-4 bg-primary text-white">
                <Image
                  className="w-75 mb-3 lazyload"
                  src="assets/images/how2.png"
                />
                <h3 className="font-weight-bold">2</h3>
                <hr className="bg-white" />
                <h5 className="font-weight-bold">CREAMOS TU ESPACIO</h5>
              </Card.Body>
            </Card>
          </div>
          <div className="mt-5 col-12 col-md-4 pl-5 pr-5 pb-4">
            <Card className="p-0">
              <Card.Body className="pl-4 pr-4">
                <Image
                  className="w-75 mb-3 lazyload"
                  src="assets/images/how3.png"
                />
                <h3 className="text-primary font-weight-bold">3</h3>
                <hr />
                <h5 className="font-weight-bold">ADMINISTRA TU NEGOCIO</h5>
              </Card.Body>
            </Card>
          </div>
        </Row>
      </div>
    );
  }
  buildCategories() {
    return (
      <fieldset>
        <Form.Group className="mt-5 pt-5 text-white">
          <Form.Label>CATEGORÍAS</Form.Label>
          <div className="col-12">
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Arte y entretenimiento"
              name="formHorizontalRadios"
              id="formHorizontalRadios1"
              value="1"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Educación"
              name="formHorizontalRadios"
              id="formHorizontalRadios2"
              value="2"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Salud"
              name="formHorizontalRadios"
              id="formHorizontalRadios13"
              value="3"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Deportes"
              name="formHorizontalRadios"
              id="formHorizontalRadios4"
              value="4"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Finanzas"
              name="formHorizontalRadios"
              id="formHorizontalRadios5"
              value="5"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Mascotas"
              name="formHorizontalRadios"
              id="formHorizontalRadios6"
              value="6"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Tecnología"
              name="formHorizontalRadios"
              id="formHorizontalRadios7"
              value="7"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Compras"
              name="formHorizontalRadios"
              id="formHorizontalRadios8"
              value="8"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Automotriz"
              name="formHorizontalRadios"
              id="formHorizontalRadios9"
              value="9"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Universidades"
              name="formHorizontalRadios"
              id="formHorizontalRadios10"
              value="10"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Comidas y bebidas"
              name="formHorizontalRadios"
              id="formHorizontalRadios11"
              value="11"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Servicios legales"
              name="formHorizontalRadios"
              id="formHorizontalRadios12"
              value="12"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Viajess"
              name="formHorizontalRadios"
              id="formHorizontalRadios13"
              value="13"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Turismo"
              name="formHorizontalRadios"
              id="formHorizontalRadios14"
              value="14"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Comercio"
              name="formHorizontalRadios"
              id="formHorizontalRadios15"
              value="15"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Construcción"
              name="formHorizontalRadios"
              id="formHorizontalRadios16"
              value="16"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Familia y Hogar"
              name="formHorizontalRadios"
              id="formHorizontalRadios17"
              value="17"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Noticias o info"
              name="formHorizontalRadios"
              id="formHorizontalRadios18"
              value="18"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Belleza"
              name="formHorizontalRadios"
              id="formHorizontalRadios19"
              value="19"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
            <Form.Check
              className="radio-custom"
              type="radio"
              label="Otro"
              name="formHorizontalRadios"
              id="formHorizontalRadios20"
              value="20"
              onChange={(e) => {
                this.state.type = e.target.value;
                this.fetchCompanies();
              }}
            />
          </div>
        </Form.Group>
      </fieldset>
    );
  }
  fetchCompanies(filterText) {
    this.setState({ loading: true });
    const location = this.location2 ? this.location2.value : null;
    CompanyRepository.find(filterText, this.state.type, location)
      .then((result) => {
        const companies =
          AuthModel.isAuthenticated && AuthModel.currentAccount.type == 1
            ? result.data
            : result.data.filter((company) => company.isActive);
        this.setState({
          companies: companies,
        });
        const locations = companies
          .filter((companyData) => {
            const company = companyData;
            if (company.latitude && company.longitude) {
              return true;
            } else if (AuthModel.isAuthenticated) {
              GeocodeAPI.locationFromAddress(company.location).then(
                (geolocation) => {
                  if (geolocation) {
                    CompanyRepository.update(
                      { latitude: geolocation.lat, longitude: geolocation.lng },
                      company.id
                    );
                  }
                }
              );
            }
            return false;
          })
          .map((company) => {
            return {
              lat: company.latitude,
              lng: company.longitude,
              name: company.name,
              id: company.id,
            };
          });
        this.setState({
          locations: locations,
        });
      })
      .catch((e) => {})
      .finally(() => this.setState({ loading: false }));
  }
}

export default CompanyListScreen;
