import React from 'react';
import './App.scss';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from 'react-router-dom';
import {
  HomeScreen,
  AboutScreen,
  TeamScreen,
  JoinScreen,
  BlogScreen,
  LoginScreen,
  SignUpScreen,
  CompanyListScreen,
  CompanyScreen,
  ProfileScreen,
  UpdateScreen,
  CreatePostScreen,
  CreateOfferScreen,
  PaymentScreen,
  PostScreen,
  RecoverPasswordScreen,
  UpdatePostScreen,
  UpdateAccountScreen,
} from './screens';
import { ScrollToTop, PrivateRoute } from './components';
import firebase from 'firebase';
import { Container, Spinner, Col } from 'react-bootstrap';
import { AuthRepository } from './repositories';
import { AuthModel } from './models';

firebase.initializeApp({
  apiKey: 'AIzaSyB4pKBQYFm0Qz79OPIC49kCgRn8kLpFGCI',
  authDomain: 'pypper-12d75.firebaseapp.com',
  databaseURL: 'https://pypper-12d75.firebaseio.com',
  projectId: 'pypper-12d75',
  storageBucket: 'pypper-12d75.appspot.com',
  messagingSenderId: '952950716704',
  appId: '1:952950716704:web:045b5f3269313a6f747f45',
  measurementId: 'G-PN2RV96S4E',
});

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    };
  }
  componentDidMount() {
    AuthModel.isAuthenticated = false;
    const storedToken = localStorage.getItem('authentication_token');
    if (storedToken) {
      AuthRepository.loginWithToken(storedToken)
        .then((response) => {
          if (response.success) {
            AuthModel.isAuthenticated = true;
            AuthModel.currentAccount.id = response.data.id;
            AuthModel.currentAccount.email = response.data.email;
            AuthModel.currentAccount.token = response.data.token;
            AuthModel.currentAccount.type = response.data.type;
            this.setState({ loading: false });
          } else {
            localStorage.removeItem('authentication_token');
            this.setState({ loading: false });
          }
        })
        .catch((e) => {
          localStorage.removeItem('authentication_token');
          this.setState({ loading: false });
        });
    } else {
      this.setState({ loading: false });
    }
  }
  render() {
    if (this.state.loading) {
      return (
        <Container
          fluid
          className="all-screen d-flex justify-content-center align-items-center bg-primary"
        >
          <Spinner
            className={{
              'd-none': !this.state.loading,
              'd-block': this.state.loading,
            }}
            as="span"
            animation="grow"
            variant="light"
            role="status"
            aria-hidden="false"
          />
        </Container>
      );
    }
    return (
      <Router>
        <ScrollToTop />
        <Switch>
          <PrivateRoute
            onlyCompany
            exact
            path="/account"
            component={ProfileScreen}
          />
          <PrivateRoute
            onlyCompany
            exact
            path="/update-account"
            component={UpdateAccountScreen}
          />
          <PrivateRoute
            onlyCompany
            exact
            path="/payment"
            component={PaymentScreen}
          />
          <PrivateRoute
            onlyCompany
            exact
            path="/create-offer"
            component={CreateOfferScreen}
          />
          <PrivateRoute
            onlyAdmin
            exact
            path="/update/:id"
            component={UpdateScreen}
          />
          <PrivateRoute
            onlyAdmin
            exact
            path="/update-post/:id"
            component={UpdatePostScreen}
          />
          <PrivateRoute
            onlyAdmin
            exact
            path="/create-post"
            component={CreatePostScreen}
          />
          <Route exact path="/company/:id" component={CompanyScreen} />
          <Route exact path="/company-list" component={CompanyListScreen} />
          <Route exact path="/sign-up" component={SignUpScreen} />
          <Route
            exact
            path="/recover-password"
            component={RecoverPasswordScreen}
          />
          <Route exact path="/login" component={LoginScreen} />
          <Route exact path="/about" component={AboutScreen} />
          <Route exact path="/team" component={TeamScreen} />
          <Route exact path="/join" component={JoinScreen} />
          <Route exact path="/blog" component={BlogScreen} />
          <Route exact path="/post/:id" component={PostScreen} />
          <Route exact path="/" component={HomeScreen} />
        </Switch>
      </Router>
    );
  }
}

export default App;
