import axios from 'axios';
import { AuthModel } from '../models';

const find = async () => {
  return (await axios.get(process.env.REACT_APP_PYPPER_API_URL + `offers`))
    .data;
};
const findById = async (offerId) => {
  return (
    await axios.get(process.env.REACT_APP_PYPPER_API_URL + `offers/${offerId}`)
  ).data;
};
const create = async (offerData) => {
  return (
    await axios({
      method: 'post',
      headers: { Authorization: AuthModel.currentAccount.token },
      url: process.env.REACT_APP_PYPPER_API_URL + `offers`,
      responseType: 'json',
      data: offerData,
    })
  ).data;
};
export default { find, create, findById };
