import './sign-up.scss';
import React from 'react';
import {
  Row,
  Col,
  Container,
  Image,
  Card,
  Button,
  Form,
  Alert,
  Modal,
  Spinner,
} from 'react-bootstrap';
import { AppBar, SearchCard, BottomBar } from '../components';
import { Link, Redirect } from 'react-router-dom';
import { AccountRepository } from '../repositories';
import { AccountModel, AuthModel } from '../models';

class UpdateAccountScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      accountId: AuthModel.currentAccount.id,
      error: false,
      loading: false,
      errorMessage:
        'Ha ocurrido un error, por favor revisa que hayas llenado todos los campos obligatorios',
      toCompanyProfile: false,
    };
  }
  componentDidMount() {
    this.email.value = AuthModel.currentAccount.email;
  }

  render() {
    if (this.state.toCompanyProfile) {
      return <Redirect to={`/account`} />;
    }
    return (
      <Container fluid>
        <AppBar secondLevel white hideOnDesktop />
        <Row className="mt-5 mt-lg-0">
          <Col
            lg={{ span: 7 }}
            className="sign-up-bg position-relative p-0 d-flex align-items-end"
          >
            <Link className="sign-logo d-none d-lg-block" to="/">
              <Image
                src="../assets/images/logo-white-border.png"
                className="logo-szie m-4"
              />
            </Link>
            <Image
              className="half-clip d-lg-none"
              src={`../assets/images/sign-up-bg.jpg`}
            />
            <div className="d-none d-lg-block mb-5 pb-3 ml-4 mr-4 w-100 text-center">
              <hr className="bg-light" />
            </div>
            <BottomBar
              className="w-100"
              hideOnMobile
              dark
              hideCopyright
              compress
            />
          </Col>
          <Col
            className="p-0 pt-2 form-container text-lg-left text-center"
            lg={{ span: 5 }}
          >
            <Col className="mt-lg-5 mb-5 mb-lg-0" lg={{ offset: 2, span: 10 }}>
              <h1 className="title">ACTUALIZAR DATOS DE CUENTA</h1>
              {this.buildForm()}
            </Col>
          </Col>
        </Row>
      </Container>
    );
  }
  buildForm() {
    return (
      <Form className="pl-lg-0 mb-5 text-left col-10 offset-1 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-10 offset-lg-0">
        <Form.Group controlId="formEmail">
          <Form.Label>Correo electrónico *</Form.Label>
          <Form.Control
            type="email"
            placeholder="Email"
            ref={(c) => (this.email = c)}
          />
        </Form.Group>
        <Form.Group controlId="formPassword">
          <Form.Label>Nueva contraseña PYPPER *</Form.Label>
          <Form.Control
            type="password"
            placeholder="Nueva contraseña"
            ref={(c) => (this.password = c)}
          />
        </Form.Group>
        {this.buildError()}
        <div className="col-12 d-flex justify-content-center">
          <Spinner
            className={{
              'd-none': !this.state.loading,
              'd-block': this.state.loading,
            }}
            as="span"
            animation="border"
            size="md"
            role="status"
            aria-hidden="false"
          />
        </div>
        <Button
          className="col-12 mt-5"
          variant="primary"
          onClick={this.onSubmit.bind(this)}
        >
          Actualizar datos
        </Button>
      </Form>
    );
  }

  buildError() {
    if (!this.state.error) {
      return null;
    }
    return <Alert variant="danger">{this.state.errorMessage}</Alert>;
  }

  async onSubmit(e) {
    this.setState({ loading: true });
    try {
      if (!this.validateFields()) {
        this.setState({ error: true, loading: false });
        return;
      }
      const company = {
        email: this.email.value,
        password: this.password.value,
      };
      await AccountRepository.update(company, this.state.accountId);
      AuthModel.currentAccount.email = company.email;
      this.setState({ toCompanyProfile: true, loading: false });
    } catch (e) {
      this.setState({
        loading: false,
        error: true,
        errormessage:
          'Ha ocurrido un error, por favor revisa que hayas llenado todos los campos obligatorios',
      });
    }
  }

  validateFields() {
    if (
      this.email.value.length < 5 ||
      !this.email.value.includes('@') ||
      !this.email.value.includes('.')
    ) {
      this.setState({
        errorMessage: 'Introduce un email válido',
      });
      return false;
    }
    if (this.password.value.length < 6) {
      this.setState({
        errorMessage: 'La contraseña debe tener 6 caracteres o más',
      });
      return false;
    }
    return true;
  }
}

export default UpdateAccountScreen;
