import axios from 'axios';
import { trackEvent } from '../common/api/facebook-pixel';
import { gtag_report_conversion } from '../common/api/google-adds';
import { AuthModel } from '../models';

const create = async (company) => {
  return (
    await axios.post(
      process.env.REACT_APP_PYPPER_API_URL + 'companies',
      company
    )
  ).data;
};

const update = async (companyData, companyId) => {
  return (
    await axios({
      method: 'patch',
      headers: { Authorization: AuthModel.currentAccount.token },
      url: process.env.REACT_APP_PYPPER_API_URL + `companies/${companyId}`,
      responseType: 'json',
      data: companyData,
    })
  ).data;
};

const sendQuotation = async (quotationData) => {
  return (
    await axios({
      method: 'post',
      headers: { Authorization: AuthModel.currentAccount.token },
      url: process.env.REACT_APP_PYPPER_API_URL + `companies/send-quotation`,
      responseType: 'json',
      data: quotationData,
    })
  ).data;
};

const sendQuestion = async (quotationData) => {
  return (
    await axios({
      method: 'post',
      headers: { Authorization: AuthModel.currentAccount.token },
      url: process.env.REACT_APP_PYPPER_API_URL + `companies/send-question`,
      responseType: 'json',
      data: quotationData,
    })
  ).data;
};

const find = async (filterText, type, location) => {
  trackEvent('Search');
  gtag_report_conversion('AW-647239265/Jp2xCOvvh84BEOGs0LQC');
  return (
    await axios.get(
      process.env.REACT_APP_PYPPER_API_URL +
        `companies?filterText=${filterText ? filterText : ''}` +
        (type ? `&type=${type}` : '') +
        (location ? `&location=${location}` : '')
    )
  ).data;
};
const findById = async (companyId) => {
  return (
    await axios.get(
      process.env.REACT_APP_PYPPER_API_URL +
        `companies/${companyId ? companyId : '-1'}`
    )
  ).data;
};

const findByAccount = async (accountId) => {
  return (
    await axios.get(
      process.env.REACT_APP_PYPPER_API_URL + `companies?accountId=${accountId}`
    )
  ).data;
};

export default {
  create,
  find,
  findById,
  findByAccount,
  update,
  sendQuotation,
  sendQuestion,
};
