import axios from 'axios';
import { AuthModel } from '../models';

const update = async (companyData, accountId) => {
  return (
    await axios({
      method: 'patch',
      headers: { Authorization: AuthModel.currentAccount.token },
      url: process.env.REACT_APP_PYPPER_API_URL + `accounts/${accountId}`,
      responseType: 'json',
      data: companyData,
    })
  ).data;
};

export default {
  update,
};
