import Geocode from 'react-geocode';

Geocode.setApiKey('AIzaSyBhHQxdTl11Yxp67EQ-JfCo1Lr78ydw9BE');
Geocode.setLanguage('es');
Geocode.setRegion('co');
Geocode.enableDebug();
const locationFromAddress = async (address) => {
  return await Geocode.fromAddress(address)
    .then((response) => {
      const { lat, lng } = response.results[0].geometry.location;
      return { lat, lng };
    })
    .catch((error) => {
      return null;
    });
};

const fromLatLng = async (lat, lng) => {
  return await Geocode.fromLatLng(lat, lng)
    .then((response) => {
      const address = response.results[0].formatted_address;
      return address;
    })
    .catch((error) => {
      return null;
    });
};

export default { locationFromAddress, fromLatLng };
