import React from 'react';
import {
  Container,
  Row,
  Image,
  Button,
  Form,
  Spinner,
  Alert,
} from 'react-bootstrap';

import { PostRepository } from '../repositories';
import {
  AppBar,
  BottomBar,
  ContactInfo,
  BottomBarResponsive,
} from '../components';
import { AuthModel } from '../models';
import { Link, Redirect } from 'react-router-dom';
import firebase from 'firebase';

class UpdatePostScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      postId: this.props.match.params.id,
      post: {},
      error: false,
      loading: false,
      errorMessage:
        'Ha ocurrido un error, por favor revisa que hayas llenado todos los campos obligatorios',
      image1PhotoName: 'Seleccionar archivo',
      image2PhotoName: 'Seleccionar archivo',
      image1PhotoFile: null,
      image2PhotoFile: null,
      toBlog: false,
    };
  }
  componentDidMount() {
    this.fetchPost();
  }
  render() {
    if (this.state.toBlog) {
      return <Redirect to={`/post/${this.state.postId}`} />;
    }
    return (
      <Container fluid>
        <AppBar white secondLevel />
        <Row className="mt-5 pt-5">
          <div className="col-12 text-center">
            <h2 className="title">¡NUEVO POST!</h2>

            <Form.Group controlId="formTitle">
              <Form.Label>Título</Form.Label>
              <Form.Control
                type="text"
                placeholder="Título"
                ref={(c) => (this.title = c)}
              />
            </Form.Group>
            <Form.Group controlId="formContent">
              <Form.Label>contenido *</Form.Label>
              <Form.Control
                as="textarea"
                rows="10"
                placeholder="Contenido"
                ref={(c) => (this.content = c)}
              />
            </Form.Group>

            <Form.Group controlId="formSeacrhPhoto">
              <Form.Label>Foto de búsqueda</Form.Label>
              <Form.File
                custom
                label={this.state.image1PhotoName}
                accept="image/x-png,image/jpeg"
                multiple={false}
                data-browse="Buscar"
                lang="es"
                onChange={() => {
                  const file = this.image1.files[0];
                  this.setState({
                    image1PhotoName: file.name,
                    image1PhotoFile: file,
                  });
                }}
                ref={(c) => (this.image1 = c)}
              />
            </Form.Group>

            <Form.Group controlId="formPhoto">
              <Form.Label>Foto de vista</Form.Label>
              <Form.File
                custom
                label={this.state.image2PhotoName}
                accept="image/x-png,image/jpeg"
                multiple={false}
                data-browse="Buscar"
                lang="es"
                onChange={() => {
                  const file = this.image2.files[0];
                  this.setState({
                    image2PhotoName: file.name,
                    image2PhotoFile: file,
                  });
                }}
                ref={(c) => (this.image2 = c)}
              />
            </Form.Group>

            {this.buildError()}
            <div className="col-12 d-flex justify-content-center">
              <Spinner
                className={{
                  'd-none': !this.state.loading,
                  'd-block': this.state.loading,
                }}
                as="span"
                animation="border"
                size="md"
                role="status"
                aria-hidden="false"
              />
            </div>
            <Button
              className="col-12 mt-5"
              variant="primary"
              onClick={this.onSubmit.bind(this)}
              disabled={this.state.loading}
            >
              Actualizar
            </Button>
          </div>
        </Row>
        <ContactInfo secondLevel />
        <BottomBar unfixed />
        <BottomBarResponsive secondLevel />
      </Container>
    );
  }
  fetchPost() {
    PostRepository.findById(this.state.postId)
      .then((response) => {
        const post = response.data;
        this.title.value = post.title;
        this.content.value = post.content;
        this.setState({
          post: post,
        });
      })
      .catch((e) => null)
      .finally(() => this.setState({ loading: false }));
  }
  buildError() {
    if (!this.state.error) {
      return null;
    }
    return <Alert variant="danger">{this.state.errorMessage}</Alert>;
  }
  async onSubmit(e) {
    this.setState({ loading: true });
    try {
      const image1Link =
        (await this.uploadPhoto(this.state.image1PhotoFile)) ||
        this.state.post.image1Link;
      const image2Link =
        (await this.uploadPhoto(this.state.image2PhotoFile)) ||
        this.state.post.image2Link;
      const post = {
        title: this.title.value,
        content: this.content.value,
        image1Link: image1Link,
        image2Link: image2Link,
      };
      await PostRepository.update(post, this.state.postId);

      this.setState({ toBlog: true, loading: false });
    } catch (error) {
      alert(error.message);
      this.setState({
        loading: false,
        error: true,
        errormessage:
          'Ha ocurrido un error, por favor revisa que hayas llenado todos los campos obligatorios',
      });
    }
  }

  async uploadPhoto(file) {
    if (!file) {
      return null;
    }
    const snapshot = await firebase
      .storage()
      .ref(`/POSTS/${new Date().getTime() + file.name}`)
      .put(file);
    return snapshot.ref.getDownloadURL();
  }
}

export default UpdatePostScreen;
