import './home.scss';
import React from 'react';
import { Row, Col, Container, Image, Card, Button } from 'react-bootstrap';
import { AppBar, SearchCard, BottomBar, ContactInfo } from '../components';

class HomeScreen extends React.Component {
  render() {
    return (
      <Container fluid className="bg-image">
        <AppBar />
        <Row className="text-center h-lg-100">
          <Image
            className="half-clip d-lg-none"
            src={`assets/images/home-bg-light.webp`}
          />
          <div className="col-5 h-100 d-none d-lg-block">
            {this.buildSocialNetworkButtons()}
          </div>
          <Col
            className="h-100 d-flex justify-content-center align-items-lg-center"
            lg={{ span: 7 }}
          >
            <div className="search-card-contaier">
              <SearchCard showCompannyButton showOffersButton />
            </div>
          </Col>
        </Row>
      </Container>
    );
  }
  buildSocialNetworkButtons() {
    return (
      <div className="social-buttons bg-primary p-2">
        <a target="blank" href="https://www.facebook.com/PYPPERCOL">
          <Image
            className="social-button lazyload"
            src="assets/images/facebook.png"
          />
        </a>
        <a target="blank" href="https://www.instagram.com/pypper_col/">
          <Image
            className="social-button lazyload"
            src="assets/images/instagram.png"
          />
        </a>
      </div>
    );
  }
}

export default HomeScreen;
