import './payment.scss';
import React from 'react';
import {
  Row,
  Col,
  Container,
  Image,
  Card,
  Button,
  Form,
  Alert,
  Modal,
  Spinner,
} from 'react-bootstrap';
import { AppBar, SearchCard, BottomBar } from '../components';
import { Link, Redirect } from 'react-router-dom';
import { CompanyRepository } from '../repositories';
import firebase from 'firebase';
import { trackEvent } from '../common/api/facebook-pixel';
import { gtag_report_conversion } from '../common/api/google-adds';
import { AuthModel } from '../models';

class PaymentScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: false,
      loading: false,
      errorMessage:
        'Ha ocurrido un error, por favor revisa que hayas llenado todos los campos obligatorios',
      profilePhotoName: 'Seleccionar archivo',
      companyLogoName: 'Seleccionar archivo',
      teamPhotoName: 'Seleccionar archivo',
      showModal: false,
      toAccount: false,
      isPayment: false,
      company: { name: '', description: '', phoneNumber: '', email: '' },
    };
  }
  componentDidMount() {
    this.fetchCompany();
  }
  render() {
    if (this.state.toAccount) {
      return <Redirect to="/account" />;
    }
    return (
      <Container fluid>
        <AppBar white />
        <Row className="mt-5 mt-lg-0">
          <Col
            lg={{ span: 4 }}
            className="payment-bg position-relative p-0 d-flex align-items-end"
          >
            <Image
              className="half-clip d-lg-none "
              src={`assets/images/payment.png`}
            />
            {this.buildSocialNetworkButtons()}
          </Col>
          <Col
            className="p-0 pt-5 form-container form-payment-decoration text-lg-left text-center"
            lg={{ span: 8 }}
          >
            <Col
              className="mt-lg-5 mb-5 pr-4 pl-4 pr-lg-0 pl-lg-0"
              lg={{ offset: 1, span: 10 }}
            >
              <div className="col-12 text-lg-right w-100 pr-lg-5 mb-4 mb-lg-2">
                <Link to="/account">
                  <Button variant="outline-primary">
                    <Image
                      className="button-icon-left"
                      src="assets/images/go-back-icon.png"
                    />
                    Volver
                  </Button>
                </Link>
              </div>
              <h1 className="title mb-5 col-12 text-center">
                ¡REALIZA TU PAGO!
              </h1>
              <Row>
                {this.buildCard()}
                {this.buildForm()}
              </Row>
              <Row className="pt-5">
                <Image
                  className="w-50 lazyload"
                  src="assets/images/payment-icons.png"
                />
              </Row>
            </Col>
          </Col>
        </Row>
        {this.buildModal()}
      </Container>
    );
  }
  buildCard() {
    return (
      <Card className="col-lg-5 p-0 mb-5 mb-lg-0">
        <Card.Body className="p-0">
          <div className="col-12 bg-primary text-center">
            <h2 className="title-white text-white pt-3 pb-3">INCLUYE</h2>
          </div>

          <Image className="w-100 lazyload" src="assets/images/separator.png" />
          <ul className="pr-4">
            <li className="mb-4 join-description">
              Tu espacio en Pypper (Visible también desde Google)
            </li>
            <li className="mb-4 join-description">Visibilidad de productos</li>
            <li className="mb-4 join-description">
              1 Promoción mensual dentro de Pypper
            </li>
            <li className="mb-4 join-description">
              1 Publicación promocionada en Facebook o Instagram de Pypper
              (Incluye diseño del anuncio)
            </li>
            <li className="mb-4 join-description">
              Servicio de asesoría del equipo de Pypper
            </li>
            <li className="mb-4 join-description">
              Guía para crear redes de aliados (Conexión con otras MIPYMES)
            </li>
          </ul>
          <Image className="w-100 lazyload" src="assets/images/separator.png" />
        </Card.Body>
        <Card.Footer className="text-center">
          <a href={this.state.company.paymentLink} target="blank">
            <Button
              variant="primary"
              onClick={() =>
                this.setState({ showModal: true, isPayment: true })
              }
            >
              PAGAR
            </Button>
          </a>
        </Card.Footer>
      </Card>
    );
  }
  buildForm() {
    return (
      <Card className="col-lg-5 offset-lg-1 p-0">
        <Card.Body className="p-0">
          <div className="col-12 bg-primary text-center">
            <h5 className="title-white text-white pt-3 pb-3">
              ¿DESEAS CLIENTES POTENCIALES GARANTIZADOS?
            </h5>
          </div>

          <Image className="w-100 lazyload" src="assets/images/separator.png" />
          <Form className="p-5">
            <h5 className="text-primary text-center mb-5">
              Puedes cotizar este servicio adicional con nuestro equipo
            </h5>
            <Form.Group controlId="formProduct">
              <Form.Label>Servicio o producto *</Form.Label>
              <Form.Control
                type="text"
                placeholder="Servicio o producto"
                ref={(c) => (this.product = c)}
              />
            </Form.Group>
            <Form.Group controlId="formLocation">
              <Form.Label>¿En dónde? *</Form.Label>
              <Form.Control
                type="text"
                placeholder="¿Donde?"
                ref={(c) => (this.location = c)}
              />
            </Form.Group>
          </Form>

          <Image className="w-100 lazyload" src="assets/images/separator.png" />
        </Card.Body>
        <Card.Footer className="text-center">
          {this.buildError()}
          <div className="col-12 d-flex justify-content-center">
            <Spinner
              className={{
                'd-none': !this.state.loading,
                'd-block': this.state.loading,
              }}
              as="span"
              animation="border"
              size="md"
              role="status"
              aria-hidden="false"
            />
          </div>
          <Button variant="primary" onClick={this.onSubmit.bind(this)}>
            COTIZAR
          </Button>
        </Card.Footer>
      </Card>
    );
  }

  buildModal() {
    return (
      <Modal
        centered
        show={this.state.showModal}
        onHide={this.closeModal.bind(this)}
        size="lg"
      >
        <Modal.Body className="bg-primary text-white">
          <Image
            src="assets/images/logo-white.png"
            className="modal-logo lazyload"
          />
          <Col className="text-center">
            <Image
              src="assets/images/quotation.png"
              className="message-logo mb-2 lazyload"
            />
            <h5 className="font-weight-bold">
              {this.state.isPayment
                ? 'GRACIAS POR TU PAGO, SIGUE DISFRUTANDO DE LOS BENEFICIOS DE PYPPER'
                : 'ESTAMOS VALIDANDO TU INFORMACIÓN PRONTO NOS PONDRÉMOS EN CONTACTO'}
            </h5>
          </Col>
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-center">
          <Button variant="primary" onClick={this.closeModal.bind(this)}>
            Entendido
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
  closeModal() {
    this.setState({ showModal: false, toAccount: true });
  }

  buildSocialNetworkButtons() {
    return (
      <div className="social-buttons bg-primary p-2 d-none d-lg-block">
        <a target="blank" href="https://www.facebook.com/PYPPERCOL">
          <Image
            className="social-button lazyload"
            src="assets/images/facebook.png"
          />
        </a>
        <a target="blank" href="https://www.instagram.com/pypper_col/">
          <Image
            className="social-button lazyload"
            src="assets/images/instagram.png"
          />
        </a>
      </div>
    );
  }

  buildError() {
    if (!this.state.error) {
      return null;
    }
    return <Alert variant="danger">{this.state.errorMessage}</Alert>;
  }
  async onSubmit(e) {
    this.setState({ loading: true });
    try {
      if (!this.validateFields()) {
        this.setState({ error: true, loading: false });
        return;
      }
      const quotation = {
        product: this.product.value,
        location: this.location.value,
      };
      await CompanyRepository.sendQuotation(quotation);

      this.setState({ showModal: true, loading: false });
    } catch (e) {
      this.setState({
        loading: false,
        error: true,
        errormessage:
          'Ha ocurrido un error, por favor revisa que hayas llenado todos los campos obligatorios',
      });
    }
  }
  fetchCompany() {
    CompanyRepository.findByAccount(AuthModel.currentAccount.id)
      .then((result) =>
        result.data[0] ? this.setState({ company: result.data[0] }) : null
      )
      .catch((e) => {});
  }
  validateFields() {
    if (this.product.value.length < 3) {
      this.setState({
        errorMessage: 'Debes ingresar un producto válido',
      });
      return false;
    }
    if (this.location.value.length < 3) {
      this.setState({
        errorMessage: 'Debes ingresar una ubicación válida',
      });
      return false;
    }
    return true;
  }
}

export default PaymentScreen;
