import React from 'react';
import {
  Container,
  Row,
  Form,
  Col,
  Card,
  Image,
  Button,
  Carousel,
  Modal,
} from 'react-bootstrap';
import queryString from 'query-string';
import {
  AppBar,
  SearchCard,
  ContactInfo,
  BottomBar,
  BottomBarResponsive,
} from '../components';
import { CompanyRepository, CompanyViewRepository } from '../repositories';
import { AuthModel } from '../models';
import { Link } from 'react-router-dom';
class CompanyScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      companyId: this.props.match.params.id,
      company: { name: '', description: '', phoneNumber: '', email: '' },
      isLoggedIn: AuthModel.isAuthenticated,
      accountType: AuthModel.currentAccount.type,
      showHidden: false,
    };
  }
  componentDidMount() {
    this.fetchCompany(this.state.companyId);
  }
  render() {
    return (
      <Container fluid>
        <AppBar white secondLevel />
        <Row className="bg-primary mt-5 pt-5 d-lg-none ">
          <div className="col-12 font-weight-bold text-white text-uppercase">
            <h2>{this.state.company.name}</h2>
          </div>
        </Row>

        <Row className="mt-lg-5 pt-lg-3">
          <div className="d-none d-lg-block col-3 bg-primary text-white pt-3 pl-5 font-weight-bold account-lines">
            <div className="col-10 offset-2">
              <h4 className="text-uppercase title-white">
                {this.state.company.name}
              </h4>
              <h4
                className="mt-5 pt-5 clicked-text title-white"
                onClick={() =>
                  this.setState({ showHidden: !this.state.showHidden })
                }
              >
                PRODUCTOS
              </h4>
              {this.state.showHidden ? (
                <div className="descrption-container mt-5">
                  {this.state.company.description.split('\n').map((text) => (
                    <p className="list-item-description">{text}</p>
                  ))}
                </div>
              ) : (
                <div>
                  <hr className="bg-white" />
                  <Carousel>
                    {this.buildCarouselItem(this.state.company.product1Link)}
                    {this.buildCarouselItem(this.state.company.product2Link)}
                    {this.buildCarouselItem(this.state.company.product3Link)}
                  </Carousel>
                </div>
              )}
              <div className="mt-5 pt-5 ">
                <h4 className="mt-5 pt-5 title-white">PROMOCIÓN DEL MES</h4>
              </div>
              <hr className="bg-white" />
              <div className="mt-5 mb-5 pt-5 pb-5" />
            </div>
          </div>
          <div className="col-12 col-lg-9">
            <Row className="company-decoration">
              <div className="col-12 col-lg-4 offset-lg-2">
                <Image
                  className="company-logo"
                  src={this.state.company.logoImageLink}
                />
                <h4
                  className="clicked-text text-primary mt-5 mb-5 title border-button"
                  onClick={() =>
                    this.setState({ showHidden: !this.state.showHidden })
                  }
                >
                  NOSOTROS
                </h4>
                {this.state.showHidden ? <hr /> : null}
                {this.state.showHidden ? (
                  <div>
                    <div className="d-none d-lg-block pb-3">
                      <h5 className="title">EQUIPO</h5>
                      <Image
                        className="w-100"
                        src={this.state.company.teamPictureLink}
                      />
                    </div>
                    <Carousel className="d-lg-none pb-3">
                      {this.buildCarouselItem(this.state.company.product1Link)}
                      {this.buildCarouselItem(this.state.company.product2Link)}
                      {this.buildCarouselItem(this.state.company.product3Link)}
                    </Carousel>
                  </div>
                ) : (
                  <div className="descrption-container bg-white bg-lg-transparent mr-4 mr-lg-0 mb-4 mb-lg-0">
                    {this.state.company.description.split('\n').map((text) => (
                      <p className="list-item-description">{text}</p>
                    ))}
                  </div>
                )}
              </div>
              <div className="col-12 col-lg-4 offset-lg-1">
                <Card className="mt-lg-5 mr-lg-5 mb-4 mb-lg-0 card-border">
                  <Card.Body className="profile-card pl-4 pb-5 pr-4 text-center">
                    <Image
                      className="w-100 profile-photo"
                      src={this.state.company.teamPictureLink}
                    />

                    <h5 className="text-primary contact-text mt-5 text-uppercase">
                      {this.state.company.name}
                    </h5>
                    <h5 className="text-primary contact-text mt-3 text-uppercase">
                      {this.state.company.email}
                    </h5>
                    <h5 className="text-primary contact-text mt-3 mb-5 text-uppercase">
                      {this.state.company.phoneNumber}
                    </h5>
                    <div>
                      <a href={'mailto:' + this.state.company.email}>
                        <Button className="w-75" variant="primary">
                          Contactar
                        </Button>
                      </a>
                    </div>
                    <div>
                      <a
                        target="blank"
                        href={
                          'https://api.whatsapp.com/send?phone=57' +
                          this.state.company.phoneNumber
                        }
                      >
                        <Button className="w-75 mt-3" variant="primary">
                          Enviar whatsapp
                        </Button>
                      </a>
                    </div>

                    {this.state.isLoggedIn && this.state.accountType == 1 ? (
                      <div>
                        <Link to={`/update/${this.state.company.id}`}>
                          <Button className="w-75 mt-3" variant="primary">
                            Editar
                          </Button>
                        </Link>
                      </div>
                    ) : null}
                  </Card.Body>
                </Card>
              </div>
            </Row>
            <Row className="bg-primary text-white">
              <h4 className="col-lg-5 contact-text pt-5 pb-5 offset-lg-2">
                ¡No te pierdas la promoción del mes!
              </h4>
              <h4 className="social-container col-lg-5 pb-2 pt-lg-5 pb-lg-5">
                <a target="blank" href={this.state.company.facebookUser}>
                  <Image
                    src="../assets/images/facebook.png"
                    className="icon-szie mr-3"
                  />
                  Facebook
                </a>
                <a
                  className="ml-5"
                  target="blank"
                  href={this.state.company.instagramUser}
                >
                  <Image
                    src="../assets/images/instagram.png"
                    className="icon-szie mr-3"
                  />
                  Instagram
                </a>
              </h4>
            </Row>
            <div className="col-lg-10  offset-lg-2 pt-5">
              {this.state.company.offerLink ? (
                <Image
                  onClick={() => this.setState({ showModal: true })}
                  className="offer-image lazyload"
                  src={this.state.company.offerLink}
                />
              ) : null}
            </div>
          </div>
          <hr />
        </Row>

        <Row>
          <div className="d-none d-lg-block col-3 bg-primary pt-3 pl-5"></div>
          <div className="col-12 col-lg-9"></div>
        </Row>
        <ContactInfo secondLevel />
        <BottomBar unfixed />
        <BottomBarResponsive secondLevel />
        {this.buildModal()}
      </Container>
    );
  }
  buildModal() {
    return (
      <Modal
        centered
        show={this.state.showModal}
        onHide={this.closeModal.bind(this)}
        size="lg"
      >
        <Modal.Header closeButton>
          <h3 className="text-primary">PROMOCIÓN</h3>
        </Modal.Header>
        <Modal.Body>
          <Image
            src={this.state.company.offerLink}
            className="lazyload w-100 h-100"
          />
        </Modal.Body>
        <Modal.Footer>
          <div>
            <a href={'mailto:' + this.state.company.email}>
              <Button
                variant="primary"
                onClick={() => this.setState({ showModal: false })}
              >
                Contactar
              </Button>
            </a>
          </div>
        </Modal.Footer>
      </Modal>
    );
  }
  buildCarouselItem(image) {
    if (!image) {
      return null;
    }
    return (
      <Carousel.Item>
        <Image className="w-100 lazyload" src={image} />
      </Carousel.Item>
    );
  }
  closeModal() {
    this.setState({ showModal: false, toAccount: true });
  }

  fetchCompany() {
    CompanyViewRepository.create(this.state.companyId);
    CompanyRepository.findById(this.state.companyId)
      .then((result) => this.setState({ company: result.data }))
      .catch((e) => {});
  }
}

export default CompanyScreen;
