import './team.scss';
import React from 'react';
import {
  Row,
  Col,
  Container,
  Image,
  Card,
  Button,
  Form,
  Alert,
  Spinner,
  Carousel,
} from 'react-bootstrap';
import {
  AppBar,
  ContactInfo,
  BottomBar,
  BottomBarResponsive,
} from '../components';
import { PostRepository } from '../repositories';
import { Link } from 'react-router-dom';
import { AuthModel } from '../models';

class PostScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      postId: this.props.match.params.id,
      post: {},
      posts: [],
      loading: true,
    };
  }
  componentDidMount() {
    this.fetchPost();

    this.fetchPosts();
  }
  render() {
    return (
      <Container fluid>
        <AppBar white secondLevel />

        <Row className="mt-5 pt-5">
          <div className="col-12 text-center">
            <h2 className="title">¡BIENVENIDO A NUESTRO BLOG!</h2>
            <Image
              className="w-75 d-none d-lg-inline-block"
              src="../assets/images/blog1.jpg"
            ></Image>
            <Image
              className="w-100 d-block d-lg-none"
              src="../assets/images/blog1.jpg"
            ></Image>
            <h6 className="team-description text-gray col-lg-6 offset-lg-3 mb-5">
              Acá podrás encontrar información de valor que te ayudará a hacer
              crecer tu negocio.
            </h6>
            {AuthModel.isAuthenticated && AuthModel.currentAccount.type == 1 ? (
              <Link to={`/update-post/${this.state.postId}`}>
                <Button>Editar</Button>
              </Link>
            ) : null}
          </div>
        </Row>
        <Row className="p-0 mt-5 team-header d-flex align-items-center justify-content-center">
          <h2 className="text-uppercase text-white title-white">
            {this.state.post.title}
          </h2>
        </Row>
        {this.buildcontent()}
        <div className="col-lg-4 offset-lg-4 mt-5">
          <Carousel>
            {this.state.posts.map((post) => this.buildCarouselItem(post))}
          </Carousel>
        </div>
        <ContactInfo secondLevel />
        <BottomBar unfixed center />
        <BottomBarResponsive secondLevel />
      </Container>
    );
  }
  buildCarouselItem(post) {
    if (!post || post.id == this.state.post.id) {
      return null;
    }
    return (
      <Carousel.Item>
        <Card>
          <Card.Body>
            <Image className="w-100 lazyload" src={post.image1Link} />
            <h3 className="title mt-2">{post.title}</h3>
            <Link to={`/post/${post.id}`}>
              <Button
                className="mt-3"
                variant="primary"
                onClick={() => {
                  this.setState({ postId: post.id, loading: true });
                  this.fetchPost();
                }}
              >
                Ver más
              </Button>
            </Link>
          </Card.Body>
          <Card.Footer className="p-0 bg-primary pt-5"></Card.Footer>
        </Card>
      </Carousel.Item>
    );
  }
  buildcontent() {
    if (this.state.loading) {
      return (
        <div className="col-12 d-flex justify-content-center mt-5">
          <Spinner
            className={{
              'd-none': !this.state.loading,
              'd-block': this.state.loading,
            }}
            as="span"
            animation="border"
            size="md"
            role="status"
            aria-hidden="false"
          />
        </div>
      );
    }
    return (
      <Row>
        <h2 className="bg-light col-12 team-description p-5 blue-lines">
          <div className="pt-5" />
          {this.state.post.content.split('\n').map((text) => (
            <p className="pl-5 pr-lg-5">{text}</p>
          ))}
          <div className="pt-5 mt-5" />
        </h2>
      </Row>
    );
  }
  fetchPost() {
    PostRepository.findById(this.state.postId)
      .then((response) => {
        this.setState({ post: response.data });
      })
      .catch((e) => null)
      .finally(() => this.setState({ loading: false }));
  }
  fetchPosts() {
    PostRepository.find()
      .then((result) =>
        this.setState({
          posts: result.data,
        })
      )
      .catch((e) => {});
  }
}

export default PostScreen;
