import './login.scss';
import React from 'react';
import {
  Button,
  Form,
  Alert,
  Container,
  Image,
  Card,
  Row,
  Col,
} from 'react-bootstrap';
import { AuthRepository } from '../repositories/';
import { BottomBar } from '../components';
import { Redirect, Link } from 'react-router-dom';
import { AuthModel } from '../models';
class RecoverPasswordScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: false,
      toLogin: false,
    };
  }
  render() {
    if (this.state.toLogin) {
      return <Redirect to={'/login'} />;
    }
    return (
      <Container fluid className="login-bg">
        <Link to="/">
          <Image
            src="assets/images/logo-white-border.png"
            className="logo-szie m-4"
          />
        </Link>
        <Col className="align-items-center justify-content-center mt-3">
          <Card className="pl-4 pr-4 pt-3 pb-3 col-12 offset-sm-2 col-sm-8 offset-md-3 col-md-6 col-lg-4 offset-lg-4">
            <Card.Body>
              <div className="text-center mb-4">
                <h1 className="title-light text-login">RECUPERAR CUENTA</h1>
                <Card.Text className="text-gray">
                  Ingresa tu email para recuperar tu contraseña
                </Card.Text>
              </div>
              <Form>
                <Form.Group controlId="formBasicEmail">
                  <Form.Label>Tu email</Form.Label>
                  <Form.Control
                    type="email"
                    placeholder="Email"
                    ref={(c) => (this.email = c)}
                  />
                </Form.Group>
                {this.buildError()}
                <Button
                  className="col-12 mt-3"
                  variant="primary"
                  onClick={this.onSubmit.bind(this)}
                >
                  Enviar
                </Button>
                <div className="col-12 mt-3 text-center">
                  <Link to="/login">Volver a login</Link>
                </div>
              </Form>
            </Card.Body>
          </Card>
        </Col>

        <BottomBar center dark hideCopyright />
      </Container>
    );
  }
  buildError() {
    if (!this.state.error) {
      return null;
    }
    return <Alert variant="danger">El usuario no existe</Alert>;
  }
  async onSubmit(e) {
    try {
      await AuthRepository.recoverPassword(this.email.value);
      alert('Por favor revisa tu correo.');
      this.setState({
        toLogin: true,
      });
    } catch (e) {
      this.setState({ error: true });
    }
  }
}

export default RecoverPasswordScreen;
