import React from 'react';
import { Container, Row, Image, Button } from 'react-bootstrap';

import { PostRepository } from '../repositories';
import {
  AppBar,
  BottomBar,
  ContactInfo,
  BottomBarResponsive,
} from '../components';
import { AuthModel } from '../models';
import { Link } from 'react-router-dom';
class BlogScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoggedIn: AuthModel.isAuthenticated,
      accountType: AuthModel.currentAccount.type,
      posts: [],
    };
  }
  componentDidMount() {
    this.fetchPosts();
  }
  render() {
    return (
      <Container fluid>
        <AppBar white />
        <Row className="mt-5 pt-5">
          <div className="col-12 text-center">
            <h2 className="title">¡BIENVENIDO A NUESTRO BLOG!</h2>
            <Image
              className="w-75 d-none d-lg-inline-block"
              src="assets/images/blog1.jpg"
            ></Image>
            <Image
              className="w-100 d-block d-lg-none"
              src="assets/images/blog1.jpg"
            ></Image>
            <h6 className="team-description text-gray col-lg-6 offset-lg-3 mb-5">
              Acá podrás encontrar información de valor que te ayudará a hacer
              crecer tu negocio.
            </h6>
            {this.state.isLoggedIn && this.state.accountType == 1 ? (
              <Link to="create-post">
                <Button variant="primary">Crear</Button>
              </Link>
            ) : null}
            {this.buildPosts()}
          </div>
        </Row>
        <ContactInfo />
        <BottomBar unfixed />
        <BottomBarResponsive />
      </Container>
    );
  }
  buildPosts() {
    return this.state.posts.map((post, i) => {
      if (i % 2 == 0) {
        return this.buildGray(post);
      } else {
        return this.buildBlue(post);
      }
    });
  }

  buildGray(post) {
    let content = post.content;
    if (content.length > 300) {
      content = post.content.substr(0, 300) + '...';
    }
    return (
      <Row className="bg-light pt-5 pb-5  blue-lines">
        <div className="col-5 text-right d-none d-lg-block">
          <Image className="w-75 lazyload" src={post.image1Link} />
        </div>
        <div className="col-12 col-lg-7 text-left">
          <div className="col-12 col-lg-9 bg-white p-4">
            <h2 className="title">{post.title}</h2>
            <p className="team-description mb-3">{content}</p>
            <Link to={`/post/${post.id}`}>
              <Button variant="primary">ver más</Button>
            </Link>
          </div>

          <div className="col-12 col-lg-9 bg-primary p-2"></div>
        </div>
      </Row>
    );
  }

  buildBlue(post) {
    let content = post.content;
    if (content.length > 300) {
      content = post.content.substr(0, 300) + '...';
    }
    return (
      <Row className="bg-primary pt-5 pb-5 white-lines-right">
        <div className="col-12 col-lg-7 text-left">
          <div className="col-12 col-lg-9 offset-lg-3 bg-white p-4">
            <h2 className="title">{post.title}</h2>
            <p className="team-description mb-3">{content}</p>
            <Link to={`/post/${post.id}`}>
              <Button variant="primary">ver más</Button>
            </Link>
          </div>

          <div className="col-12 col-lg-9 offset-lg-3 bg-gray p-2"></div>
        </div>

        <div className="col-5 text-left d-none d-lg-block">
          <Image className="w-75 lazyload" src={post.image1Link} />
        </div>
      </Row>
    );
  }
  fetchPosts() {
    PostRepository.find()
      .then((result) =>
        this.setState({
          posts: result.data,
        })
      )
      .catch((e) => {});
  }
}

export default BlogScreen;
