import './team.scss';
import React from 'react';
import {
  Row,
  Col,
  Container,
  Image,
  Card,
  Button,
  Form,
  Alert,
} from 'react-bootstrap';
import {
  AppBar,
  ContactInfo,
  BottomBar,
  BottomBarResponsive,
} from '../components';

class TeamScreen extends React.Component {
  render() {
    return (
      <Container fluid>
        <AppBar white />
        <Row className="p-0 mt-5 team-header d-flex align-items-center justify-content-center">
          <Image className="h-25 lazyload" src="assets/images/logo-white.png" />
        </Row>
        {this.buildWhiteText(
          '¿quiénes Somos?',
          'Un grupo de amigos que se puso en los zapatos de los emprendedores de nuestro país. La solidaridad y la empatía, son los pilares de este proyecto que nació cuando pensamos en aquellas MIPYMES que no cuentan con un presupuesto elevado para promocionarse en internet. Hoy más que nunca y con la llegada del COVID-19, nos damos cuenta de la importancia que el marketing digital tiene, pero también de las limitaciones económicas que tienen los emprendedores de Colombia. Por eso y buscando una solución, comenzamos a trabajar en el desarrollo de PYPPER.',
          'assets/images/team1.jpg'
        )}
        {this.buildBlueText(
          '¿POR QUÉ LO HACEMOS?',
          '¡Porque creemos que hay ideas brillantes y de gran potencial que necesitan ser visibles al mundo! Creemos que las plataformas digitales no deben ser un imposible para aquellos que están iniciando y porque estamos seguros que juntos iremos hacia el progreso.\n ¡Todos a internet, es el momento!',
          'assets/images/team2.jpg'
        )}
        <Row className="bg-primary text-white pt-5 pl-lg-5 pl-4">
          <h2 className="text-uppercase title-white">NUESTRO EQUIPO</h2>
        </Row>
        {this.buildHeadingBlueText(
          'WILLIAM ALEJANDRO MARTINEZ',
          'Publicista y amante del marketing digital desde hace 5 años. Actualmente radica en Santiago de Chile, pero su corazón se encuentra en Colombia. Pensando en esto y en las MIPYMES del país, decidió trabajar en equipo un proyecto donde el marketing digital se encontrara al alcance de todos, dando como nacimiento a PYPPER. ',
          'assets/images/alejo.jpg'
        )}
        {this.buildHeadingWhiteText(
          'DIANA CAROLINA MARTINEZ',
          'Mercadóloga con experiencia en el sector publicitario, considera el pensamiento estratégico como un pilar fundamental para establecer una conexión efectiva entre marcas y personas, ve en PYPPER un amigo aliado para aquellas empresas que ven el mundo digital como algo inasequible.',
          'assets/images/diana.jpg'
        )}
        {this.buildHeadingBlueText(
          'SANDRA PATRICIA SÁNCHEZ',
          'Apasionada del diseño gráfico y la ilustración análoga, amores que encontró a lo largo de su trayectoria y que ha complementado con su experiencia en diseño editorial, publicitario y proyectos UI y UX.',
          'assets/images/sam.jpg'
        )}
        <ContactInfo />
        <BottomBar unfixed center />
        <BottomBarResponsive />
      </Container>
    );
  }

  buildWhiteText(title, description, image) {
    return (
      <Row className="bg-primary pl-2 pr-3 pt-5 pb-5 p-lg-5 white-lines-right">
        <Col className=" col-12 col-lg-7 pr-0 pr-lg-5 mb-lg-0 mb-5">
          <h2 className="text-uppercase title-white text-white mb-5">
            {title}
          </h2>
          <p className="team-description text-white">{description}</p>
        </Col>
        <Col className="col-12 col-lg-5 d-flex justify-content-center">
          <Image className="square-image-responsive lazyload" src={image} />
        </Col>
      </Row>
    );
  }

  buildHeadingWhiteText(title, description, image) {
    return (
      <Row className="bg-white p-lg-5 pl-2 pr-3 pt-5 pb-5 ml-lg-5 blue-lines-right">
        <Col className="col-12 col-lg-7 pr-0 pr-lg-5 mb-lg-0 mb-5 text-lg-right">
          <h1 className="team-title text-white text-uppercase bg-primary pt-4 mb-5 pb-3">
            {title}
          </h1>

          <div className="col-lg-9 offset-lg-3 team-description">
            <p className="text-lg-right">{description}</p>
          </div>
        </Col>
        <Col className="col-12 col-lg-5 d-flex justify-content-center">
          <Image className="large-image-responsive lazyload" src={image} />
        </Col>
      </Row>
    );
  }
  buildBlueText(title, description, image) {
    return (
      <Row className="bg-white pl-2 pr-3 pt-5 pb-5 p-lg-5 blue-lines-right">
        <Col
          className="col-12 pr-5 pb-lg-0 pb-5 pr-0 pr-lg-5"
          lg={{ span: 7, order: 1 }}
        >
          <h2 className="text-uppercase title mb-5">{title}</h2>
          <p className="team-description">{description}</p>
        </Col>
        <Col
          className="col-12 d-flex justify-content-center"
          lg={{ span: 5, order: 0 }}
        >
          <Image className="square-image-responsive lazyload" src={image} />
        </Col>
      </Row>
    );
  }
  buildHeadingBlueText(title, description, image) {
    return (
      <Row className="bg-primary text-white pl-2 pr-3 pt-5 pb-5 p-lg-5 white-lines-right">
        <Col
          className="col-12 pb-lg-0 pb-5 pr-0 pr-lg-5 "
          lg={{ span: 7, order: 1 }}
        >
          <h1 className="team-title text-uppercase bg-white text-primary pt-4 mb-5 pb-3">
            {title}
          </h1>

          <div className="col-lg-9 team-description">
            <p>{description}</p>
          </div>
        </Col>
        <Col
          className="col-12 d-flex justify-content-center"
          lg={{ span: 5, order: 0 }}
        >
          <Image className="large-image-responsive lazyload" src={image} />
        </Col>
      </Row>
    );
  }
}

export default TeamScreen;
