import axios from 'axios';
import { AuthModel } from '../models';

const find = async () => {
  return (await axios.get(process.env.REACT_APP_PYPPER_API_URL + `posts`)).data;
};
const findById = async (postId) => {
  return (
    await axios.get(process.env.REACT_APP_PYPPER_API_URL + `posts/${postId}`)
  ).data;
};
const create = async (postData) => {
  return (
    await axios({
      method: 'post',
      headers: { Authorization: AuthModel.currentAccount.token },
      url: process.env.REACT_APP_PYPPER_API_URL + `posts`,
      responseType: 'json',
      data: postData,
    })
  ).data;
};

const update = async (postData, postId) => {
  return (
    await axios({
      method: 'patch',
      headers: { Authorization: AuthModel.currentAccount.token },
      url: process.env.REACT_APP_PYPPER_API_URL + `posts/${postId}`,
      responseType: 'json',
      data: postData,
    })
  ).data;
};
export default { find, create, findById, update };
