import './login.scss';
import React from 'react';
import {
  Button,
  Form,
  Alert,
  Container,
  Image,
  Card,
  Row,
  Col,
} from 'react-bootstrap';
import { AuthRepository } from '../repositories/';
import { BottomBar } from '../components';
import { Redirect, Link } from 'react-router-dom';
import { AuthModel } from '../models';
class LoginScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: false,
      toAccount: AuthModel.isAuthenticated,
      toCompanyList: false,
    };
  }
  render() {
    if (this.state.toAccount) {
      return <Redirect to={'account'} />;
    }
    if (this.state.toCompanyList) {
      return <Redirect to={'company-list'} />;
    }
    return (
      <Container fluid className="login-bg">
        <Link to="/">
          <Image
            src="assets/images/logo-white-border.png"
            className="logo-szie m-4"
          />
        </Link>
        <Col className="align-items-center justify-content-center mt-3">
          <Card className="pl-4 pr-4 pt-3 pb-3 col-12 offset-sm-2 col-sm-8 offset-md-3 col-md-6 col-lg-4 offset-lg-4">
            <Card.Body>
              <div className="text-center mb-4">
                <h1 className="title-light text-login">BIENVENIDO</h1>
                <Card.Text className="text-gray">
                  Inicia sesión para entrar a tu negocio
                </Card.Text>
              </div>
              <Form>
                <Form.Group controlId="formBasicEmail">
                  <Form.Label>Tu email</Form.Label>
                  <Form.Control
                    type="email"
                    placeholder="Email"
                    ref={(c) => (this.email = c)}
                  />
                </Form.Group>

                <Form.Group controlId="formBasicPassword">
                  <Form.Label>Contraseña</Form.Label>
                  <Form.Control
                    type="password"
                    placeholder="Contraseña"
                    ref={(c) => (this.password = c)}
                  />
                </Form.Group>
                {this.buildError()}
                <Button
                  className="col-12 mt-3"
                  variant="primary"
                  onClick={this.onSubmit.bind(this)}
                >
                  Entrar
                </Button>
                <div className="col-12 mt-3 text-center">
                  <Link to="/recover-password">Olvidé mi contraseña</Link>
                </div>
              </Form>
            </Card.Body>
          </Card>
        </Col>

        <BottomBar center dark hideCopyright hideOnMobile />
      </Container>
    );
  }
  buildError() {
    if (!this.state.error) {
      return null;
    }
    return <Alert variant="danger">Los datos de acceso son incorrectos</Alert>;
  }
  async onSubmit(e) {
    try {
      const response = await AuthRepository.login(
        this.email.value,
        this.password.value
      );

      AuthModel.isAuthenticated = true;
      AuthModel.currentAccount.id = response.data.id;
      AuthModel.currentAccount.email = response.data.email;
      AuthModel.currentAccount.token = response.data.token;
      AuthModel.currentAccount.type = response.data.type;
      localStorage.setItem('authentication_token', response.data.token);
      if (response.data.type == 1) {
        this.setState({
          toCompanyList: true,
        });
      } else {
        this.setState({
          toAccount: true,
        });
      }
    } catch (e) {
      this.setState({ error: true });
    }
  }
}

export default LoginScreen;
