import './bottom-bar.scss';
import React from 'react';
import { Navbar, Nav, div, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { AuthModel } from '../models';
class BottomBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <Navbar
        bg="transparent"
        fixed={this.props.unfixed ? null : 'bottom'}
        className={{
          'bottom-bar': !this.props.compress,
          'd-none': true,
          'd-lg-block': true,
          'dark-bar': this.props.dark,
          'light-bar': !this.props.dark,
          'w-50': this.props.hideCopyright,
        }}
      >
        {this.props.center ? <div className="mr-auto" /> : null}

        <Nav className="text-center">
          <Row>
            <div className="col-4 col-lg-2 text-center">
              <Nav.Link>
                <Link to="/">HOME</Link>
              </Nav.Link>
            </div>
            <div className="col-4 col-lg-3 text-center">
              <Nav.Link>
                <Link to="/about">NOSOTROS</Link>
              </Nav.Link>
            </div>
            <div className="col-4 col-lg-2 text-center">
              <Nav.Link>
                <Link to="/team">EQUIPO</Link>
              </Nav.Link>
            </div>
            <div className="col-6 col-lg-3 text-center">
              <Nav.Link>
                <Link to="/join">QUIERO ESTAR EN PYPPER</Link>
              </Nav.Link>
            </div>
            {AuthModel.isAuthenticated ? (
              <div className=" col-6 col-lg-2 text-center">
                <Nav.Link>
                  <Link to="/login">CERRAR SESIÓN</Link>
                </Nav.Link>
              </div>
            ) : (
              <div className=" col-6 col-lg-2 text-center">
                <Nav.Link>
                  <Link to="/login">LOGIN</Link>
                </Nav.Link>
              </div>
            )}

            {this.props.hideCopyright ? null : (
              <Row>
                <div className="col-4 text-center">
                  <a
                    target="blank"
                    href="https://drive.google.com/file/d/1Yhy7SCOse98BWFT-L8Hb4iUJj4rHQ1_e/view"
                  >
                    TERMINOS Y CONDICIONES
                  </a>
                </div>
                <div className="col-4 text-center">
                  Copyright @ 2020 pypper Colombia
                </div>

                <div className="col-4 text-center">
                  <a
                    target="blank"
                    href="https://drive.google.com/file/d/1axuDX0EcvAOsDAyvFx47XF-yhfaGUtph/view"
                  >
                    POLÍTICA DE PRIVACIDAD
                  </a>
                </div>
              </Row>
            )}
          </Row>
        </Nav>
        <div className="mr-auto"></div>
      </Navbar>
    );
  }
}

export default BottomBar;
