import './bottom-bar-responsive.scss';
import React from 'react';
import { Navbar, Nav, div, Row, Image } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { AuthModel } from '../models';
class BottomBarResponsive extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <Row className="d-lg-none mt-5 mb-5">
        <div className="col-3 text-center">
          <div className="col-12 text-center mt-2">
            <Image
              className="w-100"
              src={
                this.props.secondLevel
                  ? '../assets/images/pypper-icon.png'
                  : 'assets/images/pypper-icon.png'
              }
            />
          </div>
          <a target="blank" href="https://www.facebook.com/PYPPERCOL">
            <Image
              src={
                this.props.secondLevel
                  ? '../assets/images/facebook-light.jpg'
                  : 'assets/images/facebook-light.jpg'
              }
              className="icon-szie mr-3"
            />
          </a>
          <a target="blank" href="https://www.instagram.com/pypper_col/">
            <Image
              src={
                this.props.secondLevel
                  ? '../assets/images/insta-light.jpg'
                  : 'assets/images/insta-light.jpg'
              }
              className="icon-szie"
            />
          </a>
        </div>
        <div className="col-3 bottom-container">
          <Link to="/">HOME</Link>
          <br />
          <Link to="/about">NOSOTROS</Link>
          <br />
          <Link to="/team">EQUIPO</Link>
          <br />
          {AuthModel.isAuthenticated ? (
            <Link to="/login">CERRAR SESIÓN</Link>
          ) : (
            <Link to="/login">LOGIN</Link>
          )}
        </div>
        <div className="col-6 bottom-container">
          <Link to="/join">QUIERO ESTAR EN PYPPER</Link>
          <br />
          <a
            target="blank"
            href="https://drive.google.com/file/d/1Yhy7SCOse98BWFT-L8Hb4iUJj4rHQ1_e/view"
          >
            TÉRMINOS Y CONDICIONES
          </a>
          <br />
          <a
            target="blank"
            href="https://drive.google.com/file/d/1Yhy7SCOse98BWFT-L8Hb4iUJj4rHQ1_e/view"
          >
            POLÍTICA DE DATOS
          </a>
          <br />
          <a>Copyright @ 2020 pypper Colombia</a>
        </div>
      </Row>
    );
  }
}

export default BottomBarResponsive;
