import axios from 'axios';

const login = async (email, password) => {
  return (
    await axios.post(process.env.REACT_APP_PYPPER_API_URL + 'auth', {
      email,
      password,
    })
  ).data;
};

const recoverPassword = async (email) => {
  return (
    await axios.post(
      process.env.REACT_APP_PYPPER_API_URL + 'accounts/recover-password',
      {
        email,
      }
    )
  ).data;
};

const loginWithToken = async (token) => {
  return (
    await axios({
      method: 'post',
      headers: { Authorization: token },
      url: process.env.REACT_APP_PYPPER_API_URL + 'auth',
      responseType: 'json',
    })
  ).data;
};

export default { login, loginWithToken, recoverPassword };
