import './profile.scss';
import React from 'react';
import {
  Container,
  Row,
  Form,
  Col,
  Card,
  Image,
  Button,
  Carousel,
  Modal,
} from 'react-bootstrap';
import queryString from 'query-string';
import {
  AppBar,
  SearchCard,
  ContactInfo,
  BottomBar,
  BottomBarResponsive,
} from '../components';
import { CompanyRepository } from '../repositories';
import { AuthModel } from '../models';
import { Link } from 'react-router-dom';
class ProfileScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      company: { name: '', description: '', phoneNumber: '', email: '' },
      showHidden: false,
      showChat: false,
      showModal: false,
    };
  }
  componentDidMount() {
    this.fetchCompany();
  }

  render() {
    return (
      <Container fluid>
        <AppBar white secondLevel />
        <Row className="bg-primary mt-5 pt-5 d-lg-none ">
          <div className="col-12 font-weight-bold text-white text-uppercase">
            <h2>{this.state.company.name}</h2>
          </div>
        </Row>

        <Row className="mt-lg-5 pt-lg-3">
          <div className="d-none d-lg-block col-3 bg-primary text-white pt-3 pl-5 font-weight-bold account-lines">
            <div className="col-10 offset-2">
              <h4 className="text-uppercase title-white">
                {this.state.company.name}
              </h4>
              <h4
                className="mt-5 pt-5 clicked-text title-white"
                onClick={() =>
                  this.setState({ showHidden: !this.state.showHidden })
                }
              >
                PRODUCTOS
              </h4>
              {this.state.showHidden ? (
                <div className="descrption-container mt-5">
                  {this.state.company.description.split('\n').map((text) => (
                    <p className="list-item-description">{text}</p>
                  ))}
                </div>
              ) : (
                <div>
                  <hr className="bg-white" />
                  <Carousel>
                    {this.buildCarouselItem(this.state.company.product1Link)}
                    {this.buildCarouselItem(this.state.company.product2Link)}
                    {this.buildCarouselItem(this.state.company.product3Link)}
                  </Carousel>
                </div>
              )}
              <div className="mt-5 pt-5 ">
                <h4 className="mt-5 pt-5 title-white">PROMOCIÓN DEL MES</h4>
              </div>
              <hr className="bg-white" />
              <div className="mt-5 mb-5 pt-5 pb-5" />
            </div>
          </div>
          <div className="col-12 col-lg-9">
            <div className="col-12 text-right w-100 mt-5 mt-lg-3 pr-lg-5 mb-2 p-0">
              <Link to="/create-offer">
                <Button variant="outline-primary mr-lg-3">
                  Publicar oferta
                  <Image
                    className="button-icon"
                    src="assets/images/create-offer-icon.png"
                  />
                </Button>
              </Link>
              <Link to="/payment">
                <Button variant="outline-primary">
                  Pago mensual
                  <Image
                    className="button-icon"
                    src="assets/images/payment-icon.png"
                  />
                </Button>
              </Link>
            </div>
            <Row className="company-decoration">
              <div className="col-12 col-lg-4 offset-lg-2">
                <Image
                  className="company-logo"
                  src={this.state.company.logoImageLink}
                />
                <h4
                  className="clicked-text text-primary mt-5 mb-5 title"
                  onClick={() =>
                    this.setState({ showHidden: !this.state.showHidden })
                  }
                >
                  NOSOTROS
                  {this.state.showHidden ? <hr /> : null}
                </h4>
                {this.state.showHidden ? (
                  <div>
                    <div className="d-none d-lg-block pb-3">
                      <h5 className="title">EQUIPO</h5>
                      <Image
                        className="w-100"
                        src={this.state.company.teamPictureLink}
                      />
                    </div>
                    <Carousel className="d-lg-none pb-3">
                      {this.buildCarouselItem(this.state.company.product1Link)}
                      {this.buildCarouselItem(this.state.company.product2Link)}
                      {this.buildCarouselItem(this.state.company.product3Link)}
                    </Carousel>
                  </div>
                ) : (
                  <div className="descrption-container bg-white bg-lg-transparent mr-4 mr-lg-0 mb-4 mb-lg-0">
                    {this.state.company.description.split('\n').map((text) => (
                      <p className="list-item-description">{text}</p>
                    ))}
                  </div>
                )}
              </div>
              <div className="col-12 col-lg-4 offset-lg-1">
                <Card className="mt-lg-5 mr-lg-5 mb-4 mb-lg-0 card-border">
                  <Card.Body className="profile-card pl-4 pb-5 pr-4 text-center">
                    <Image
                      className="w-100 profile-photo"
                      src={this.state.company.teamPictureLink}
                    />

                    <h5 className="text-primary contact-text mt-5 text-uppercase">
                      {this.state.company.name}
                    </h5>
                    <h5 className="text-primary contact-text mt-3 text-uppercase">
                      {this.state.company.email}
                    </h5>
                    <h5 className="text-primary contact-text mt-3 mb-5 text-uppercase">
                      {this.state.company.phoneNumber}
                    </h5>
                    <div>
                      <a href={'mailto:' + this.state.company.email}>
                        <Button className="w-75" variant="primary">
                          Contactar
                        </Button>
                      </a>
                    </div>
                    <div>
                      <a
                        target="blank"
                        href={
                          'https://api.whatsapp.com/send?phone=57' +
                          this.state.company.phoneNumber
                        }
                      >
                        <Button className="w-75 mt-3" variant="primary">
                          Enviar whatsapp
                        </Button>
                      </a>
                      <Link to="update-account">
                        <Button className="w-75 mt-3" variant="primary">
                          Actualizar datos de acceso
                        </Button>
                      </Link>
                    </div>
                  </Card.Body>
                </Card>
              </div>
            </Row>
            <Row className="bg-primary text-white">
              <h4 className="col-10 contact-text pt-5 pb-5 offset-lg-2">
                ¡No te pierdas la promoción del mes!
              </h4>
            </Row>
            <div className="col-lg-10  offset-lg-2 pt-5">
              {this.state.company.offerLink ? (
                <Image
                  onClick={() => this.setState({ showModal: true })}
                  className="offer-image lazyload"
                  src={this.state.company.offerLink}
                />
              ) : null}
            </div>
          </div>
          <hr />
        </Row>

        <Row>
          <div className="d-none d-lg-block col-3 bg-primary pt-3 pl-5"></div>
          <div className="col-12 col-lg-9"></div>
        </Row>
        <ContactInfo secondLevel />
        <BottomBar unfixed hideOnMobile />
        <BottomBarResponsive secondLevel />
        {this.buildChatIcon()}
        {this.buildChat()}
        {this.buildViewCounter()}
        {this.buildModal()}
      </Container>
    );
  }
  buildModal() {
    return (
      <Modal
        centered
        show={this.state.showModal}
        onHide={this.closeModal.bind(this)}
        size="lg"
      >
        <Modal.Header closeButton>
          <h3 className="text-primary">PROMOCIÓN</h3>
        </Modal.Header>
        <Modal.Body>
          <Image
            src={this.state.company.offerLink}
            className="lazyload w-100 h-100"
          />
        </Modal.Body>
        <Modal.Footer>
          <div>
            <a href={'mailto:' + this.state.company.email}>
              <Button
                variant="primary"
                onClick={() => this.setState({ showModal: false })}
              >
                Contactar
              </Button>
            </a>
          </div>
        </Modal.Footer>
      </Modal>
    );
  }
  buildCarouselItem(image) {
    if (!image) {
      return null;
    }
    return (
      <Carousel.Item>
        <Image className="w-100 lazyload" src={image} />
      </Carousel.Item>
    );
  }
  closeModal() {
    this.setState({ showModal: false, toAccount: true });
  }
  buildViewCounter() {
    return (
      <div className="text-white bg-primary view-counter">
        {this.state.company.views}
        <Image className="lazyload" src="assets/images/eye-icon.png" />
      </div>
    );
  }
  buildChatIcon() {
    return (
      <div
        className="bg-white chat-icon"
        onClick={() => this.setState({ showChat: !this.state.showChat })}
      >
        <Image className="lazyload" src="assets/images/chat-icon.png" />
      </div>
    );
  }

  buildChat() {
    if (!this.state.showChat) {
      return null;
    }
    return (
      <div className="chat p-3">
        <Form>
          <div className="col-12 text-center mb-3">
            <Image src="assets/images/chat-user.png" />
          </div>
          <Form.Group controlId="formName">
            <Form.Control
              type="text"
              placeholder="Nombre completo"
              ref={(c) => (this.questionName = c)}
            />
          </Form.Group>
          <Form.Group controlId="formEmail">
            <Form.Control
              type="text"
              placeholder="Tu correo electrónico"
              ref={(c) => (this.questionEmail = c)}
            />
          </Form.Group>
          <Form.Group controlId="formDescription">
            <Form.Control
              as="textarea"
              placeholder="¿Tienes alguna duda?"
              rows="3"
              ref={(c) => (this.questionDescription = c)}
            />
          </Form.Group>
          <div className="col-12 text-center">
            <Button onClick={this.sendMessage.bind(this)}>Enviar</Button>
          </div>
        </Form>
      </div>
    );
  }
  sendMessage() {
    if (
      this.questionName.value.length < 3 ||
      this.questionEmail.value.length < 3 ||
      this.questionDescription.value < 3
    ) {
      alert(
        'Por favor completa los datos de la pregunta para que podamos ayudarte.'
      );
      return;
    }
    const question = {
      name: this.questionName.value,
      email: this.questionEmail.value,
      description: this.questionDescription.value,
    };
    CompanyRepository.sendQuestion(question)
      .then(() => alert('Tu pregunta ha sido enviada con éxito'))
      .catch(() =>
        alert('Lo sentimos, ha ocurrido un error al enviar tu pregunta')
      )
      .finally(() => this.setState({ showChat: false }));
  }
  fetchCompany() {
    CompanyRepository.findByAccount(AuthModel.currentAccount.id)
      .then((result) => this.setState({ company: result.data[0] }))
      .catch((e) => {});
  }
}

export default ProfileScreen;
