import './join.scss';
import React from 'react';
import { Container, Col, Row, Card, Image, Button } from 'react-bootstrap';
import {
  AppBar,
  RightText,
  LeftText,
  ContactInfo,
  BottomBar,
  BottomBarResponsive,
} from '../components';
import { Link } from 'react-router-dom';
import { gtag_report_conversion } from '../common/api/google-adds';
class JoinScreen extends React.Component {
  render() {
    return (
      <Container fluid className="pl-0">
        <AppBar white />
        <Col className="text-center mt-5 pt-5">
          <div className="bg-primary blue-left d-none d-lg-block" />
          <h2 className="title">¡QUIERO ESTAR EN PYPPER!</h2>
          <h5 className="text-primary">¿QUÉ OBTENGO AL SER PARTE DE PYPPER?</h5>
        </Col>
        <Row className="bg-primary mt-4 pb-5 white-lines">
          <div className="col-lg-4 pt-3 d-flex justify-content-end pr-0">
            <div className="col-8 offset-2 offset-lg-4">
              <Image
                src="assets/images/another-man.jpg"
                className="w-100 lazyload"
              />
            </div>
          </div>
          <div className="col-lg-5 pt-5 pl-0">
            <Card>
              <Card.Body className="mt-5 pr-5 text-left">
                <ul>
                  <li className="mb-4 join-description">
                    Uno de nuestros objetivos es darte visibilidad digital, por
                    eso contarás con tu propio espacio en la web en donde
                    exhibirás tus productos o servicios y contarás al mundo lo
                    que haces.
                  </li>
                  <li className="mb-4 join-description">
                    ¡Nuestras redes a tu servicio! Podrás tener presencia en las
                    redes sociales de PYPPER, en donde tus productos y servicios
                    podrán ser vistos por todos mediante anuncios promocionados.
                  </li>
                  <li className="mb-4 join-description">
                    Serás parte de la comunidad de MIPYMES que se ayudan entre
                    sí para progresar. Para eso nos encargaremos de crear lazos
                    de comunicación buscando alianzas estratégicas que aporten a
                    tu negocio.
                  </li>
                  <li className="mb-4 join-description">
                    Asesoría profesional sobre cómo mejorar tu presencia en
                    internet.
                  </li>
                  <li className="mb-4 join-description">
                    El soporte y la pasión de un equipo que está dispuesto y
                    comprometido a ayudarte en lo que necesites para que tu
                    proyecto salga a adelante. ¡Nosotros creemos en ti!
                  </li>
                </ul>
              </Card.Body>
            </Card>
          </div>
        </Row>

        <RightText
          title="¿CUÁNTO ME CUESTA ESTAR EN PYPER?"
          description="Queremos ayudarte y nuestra razón de ser es llevar tu negocio al mundo digital. Durante el primer mes podrás contar con todos nuestros servicios de manera gratuita."
          headImage="assets/images/join1.jpg"
          footerImage="assets/images/about5.jpg"
        />
        <LeftText
          title="¿POR QUÉ ES GRATIS?"
          description="Entendemos la situación actual, muchas empresas y familias se han visto afectadas por el COVID-19. Te comprendemos y nuestro objetivo es poder ayudarte para que te sobrepongas de esta crisis. ¡Estamos contigo!"
          headImage="assets/images/join2.jpg"
          footerImage="assets/images/about3.jpg"
        />
        <RightText
          title="¿QUÉ PASA DESPUÉS DEL PRIMER MES?"
          description="Nuestro objetivo también es que las personas nos conozcan para hacer de este espacio un mercado digital en el cual cualquiera pueda encontrarte. Para esto requerimos de tu apoyo,  por eso y pensando en tu bienestar, hemos desarrollado los siguientes planes los cuales puedes pagar mensual sin ninguna cláusula de permanencia. Es decir, puedes cancelar en cualquier momento."
          headImage="assets/images/about6.jpg"
          footerImage="assets/images/about7.jpg"
        />
        <Col className="text-center mt-5 mb-5 pr-0">
          <h2 className="title">PAQUETE DE BIENVENIDA</h2>
          <Card className="mt-4 text-center text-primary font-weight-bold">
            <Card.Body className="mt-5">
              <div className="col-12 col-lg-8 offset-lg-2">
                <Row className="mb-3">
                  <Col></Col>
                  <Col>DESCUENTOS</Col>
                  <Col>TARIFA</Col>
                </Row>
                <Row className="mb-4">
                  <Col className="text-left">PRIMER MES</Col>
                  <Col className="text-gray">100%</Col>
                  <Col className="text-right">$0</Col>
                </Row>
                <Row className="mb-4">
                  <Col className="text-left">SEGUNDO MES</Col>
                  <Col className="text-gray">55%</Col>
                  <Col className="text-right">$50.000</Col>
                </Row>
                <Row className="mb-4">
                  <Col className="text-left">TERCER MES</Col>
                  <Col className="text-gray">0%</Col>
                  <Col className="text-right">$90.000</Col>
                </Row>
              </div>
            </Card.Body>
          </Card>
        </Col>
        <div className="col-10 offset-1 col-lg-4 offset-lg-4 bg-primary text-white text-center pt-3 pb-2">
          <h2 className="title-white">INCLUYE</h2>
        </div>
        <Row className="bg-primary join-decoration">
          <div className="col-12 col-lg-4 offset-lg-4 mb-5">
            <Card className="mb-5">
              <Card.Body className="mt-2">
                <Image
                  className="w-100 lazyload"
                  src="assets/images/separator.png"
                />
                <ul className="pr-4">
                  <li className="mb-4 join-description">
                    Tu espacio en Pypper (Visible también desde Google)
                  </li>
                  <li className="mb-4 join-description">
                    Visibilidad de productos
                  </li>
                  <li className="mb-4 join-description">
                    1 Promoción mensual dentro de Pypper
                  </li>
                  <li className="mb-4 join-description">
                    1 Publicación promocionada en Facebook o Instagram de Pypper
                    (Incluye diseño del anuncio)
                  </li>
                  <li className="mb-4 join-description">
                    Servicio de asesoría del equipo de Pypper
                  </li>
                  <li className="mb-4 join-description">
                    Guía para crear redes de aliados (Conexión con otras
                    MIPYMES)
                  </li>
                </ul>
                <Image
                  className="w-100 lazyload"
                  src="assets/images/separator.png"
                />
                <Col className="text-center">
                  <p className="text-primary font-weight-bold join-description">
                    ¿Deseas clientes potenciales garantizados?
                  </p>
                  <p className="join-description">
                    Puedes cotizar este servicio adicional con nuestro equipo
                  </p>
                </Col>
              </Card.Body>
            </Card>
            <div className="col-12 d-flex justify-content-center">
              <Link to="sign-up" className="w-75">
                <Button
                  variant="light"
                  className="w-100 text-primary join-description font-weight-bold"
                  onClick={() =>
                    gtag_report_conversion('AW-647239265/LyMSCNjih84BEOGs0LQC')
                  }
                >
                  <h5>¡Regístrate!</h5>
                </Button>
              </Link>
            </div>
          </div>
        </Row>
        <ContactInfo />
        <BottomBar unfixed center />
        <BottomBarResponsive />
      </Container>
    );
  }
}

export default JoinScreen;
