import React from 'react';
import { Button, Card, Form, Row } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { gtag_report_conversion } from '../common/api/google-adds';

class SearchCard extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      toCompanyLink: 'company-list',
    };
  }
  render() {
    return (
      <Card className="text-center search-card h-100">
        <Card.Body className="text-primary mt-4 p-0">
          <div className="ml-3 pt-4 pl-3 pl-lg-5 pr-lg-3 pt-lg-5">
            <Form className="text-left">
              <Form.Group controlId="formFilter">
                <Form.Label>¿Qué producto o servicio buscas?</Form.Label>
                <Form.Control
                  type="text"
                  onChange={() =>
                    this.setState({
                      toCompanyLink: `company-list?filterText=${this.filter.value}`,
                    })
                  }
                  placeholder="¿Qué buscas?"
                  ref={(c) => (this.filter = c)}
                />
              </Form.Group>
              <Form.Group controlId="formLocation">
                <Form.Label>¿Dónde estás?</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="¿Donde estás?"
                  ref={(c) => (this.location = c)}
                />
              </Form.Group>
            </Form>
            <Row className="mt-4">
              <div className="col-6">
                <Link to={this.state.toCompanyLink}>
                  <Button
                    variant="primary"
                    className="w-100"
                    onClick={() => {
                      if (this.props.onSearch) {
                        this.props.onSearch(this.filter.value);
                      }
                    }}
                  >
                    Buscar
                  </Button>
                </Link>
              </div>
              <div className="col-6">{this.buildOffersButton()}</div>
            </Row>
          </div>

          {this.buildCompanyButton()}
        </Card.Body>
      </Card>
    );
  }
  buildOffersButton() {
    if (!this.props.showOffersButton) {
      return null;
    }
    return (
      <div>
        <Link to="company-list?filterText=empty">
          <Button variant="outline-primary" className="w-100">
            Ver ofertas
          </Button>
        </Link>
      </div>
    );
  }
  buildCompanyButton() {
    if (!this.props.showCompannyButton) {
      return null;
    }
    return (
      <div className="text-white pb-5 mt-4 pt-lg-5 mt-lg-5 pb-0">
        <Card.Text>¿Eres una empresa?</Card.Text>

        <div className="ml-5 mr-2">
          <Link to="sign-up">
            <Button
              variant="outline-primary"
              className="w-100 bg-light"
              onClick={() =>
                gtag_report_conversion('AW-647239265/LyMSCNjih84BEOGs0LQC')
              }
            >
              Registrate
            </Button>
          </Link>
        </div>
      </div>
    );
  }
}

export default SearchCard;
