import './sign-up.scss';
import React from 'react';
import {
  Row,
  Col,
  Container,
  Image,
  Card,
  Button,
  Form,
  Alert,
  Modal,
  Spinner,
} from 'react-bootstrap';
import { AppBar, SearchCard, BottomBar } from '../components';
import { Link, Redirect } from 'react-router-dom';
import { CompanyRepository } from '../repositories';
import firebase from 'firebase';
import { trackEvent } from '../common/api/facebook-pixel';
import { gtag_report_conversion } from '../common/api/google-adds';
import { GeocodeAPI } from '../common/api';

class SignUpScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: false,
      loading: false,
      errorMessage:
        'Ha ocurrido un error, por favor revisa que hayas llenado todos los campos obligatorios',
      profilePhotoName: 'Seleccionar archivo',
      companyLogoName: 'Seleccionar archivo',
      teamPhotoName: 'Seleccionar archivo',
      productPhotoName: 'Seleccionar archivo',
      profilePhotoFile: null,
      companyLogoFile: null,
      teamPhotoFile: null,
      productPhotoFiles: [],
      showModal: false,
      toHome: false,
      showOther: false,
    };
  }
  render() {
    if (this.state.toHome) {
      return <Redirect to="/" />;
    }
    return (
      <Container fluid>
        <AppBar white hideOnDesktop />
        <Row className="mt-5 mt-lg-0">
          <Col
            lg={{ span: 7 }}
            className="sign-up-bg position-relative p-0 d-flex align-items-end"
          >
            <Link className="sign-logo d-none d-lg-block" to="/">
              <Image
                src="assets/images/logo-white-border.png"
                className="logo-szie m-4"
              />
            </Link>
            <Image
              className="half-clip d-lg-none"
              src={`assets/images/sign-up-bg.jpg`}
            />
            <div className="d-none d-lg-block mb-5 pb-3 ml-4 mr-4 w-100 text-center">
              <h5 className="text-light text-uppercase">
                ¡Inscribe tu negocio y se parte de la comunidad pypper!
              </h5>
              <hr className="bg-light" />
            </div>
            <BottomBar
              className="w-100"
              hideOnMobile
              dark
              hideCopyright
              compress
            />
          </Col>
          <Col
            className="p-0 pt-2 form-container text-lg-left text-center"
            lg={{ span: 5 }}
          >
            <Col className="mt-lg-5 mb-5 mb-lg-0" lg={{ offset: 2, span: 10 }}>
              <h1 className="title">¡BIENVENIDO!</h1>
              <p className="mb-5 pl-4 pr-4 pl-lg-0 pr-lg-0 mr-lg-5">
                Para inscribir tu empresa por favor diligencia la siguiente
                información:
              </p>
              {this.buildForm()}
            </Col>
          </Col>
        </Row>
        {this.buildModal()}
      </Container>
    );
  }
  buildForm() {
    return (
      <Form className="pl-lg-0 mb-5 text-left col-10 offset-1 col-sm-8 offset-sm-2 col-md-6 offset-md-3 col-lg-10 offset-lg-0">
        <Form.Group controlId="formName">
          <Form.Label>Tu nombre *</Form.Label>
          <Form.Control
            type="text"
            placeholder="Tu nombre"
            ref={(c) => (this.userName = c)}
          />
        </Form.Group>
        <Form.Group controlId="formName">
          <Form.Label>Nombre del negocio *</Form.Label>
          <Form.Control
            type="text"
            placeholder="Nombre del negocio"
            ref={(c) => (this.name = c)}
          />
        </Form.Group>

        <Form.Group controlId="formEmail">
          <Form.Label>Correo electrónico *</Form.Label>
          <Form.Control
            type="email"
            placeholder="Email"
            ref={(c) => (this.email = c)}
          />
        </Form.Group>

        <Form.Group controlId="formPassword">
          <Form.Label>Contraseña PYPPER *</Form.Label>
          <Form.Control
            type="password"
            placeholder="Contraseña"
            ref={(c) => (this.password = c)}
          />
        </Form.Group>

        <Form.Group controlId="formPhone">
          <Form.Label>Número de celular *</Form.Label>
          <Form.Control
            type="phone"
            placeholder="Número de celular"
            ref={(c) => (this.phoneNumber = c)}
          />
        </Form.Group>

        <Form.Group controlId="formLocation">
          <Form.Label>¿Dónde te encuentras? *</Form.Label>
          <Form.Control
            type="text"
            placeholder="Ciudad - Dirección"
            ref={(c) => (this.location = c)}
          />
        </Form.Group>
        <Form.Group controlId="formFacebook">
          <Form.Label>¿Cuál es tu página facebook? (Opcional)</Form.Label>
          <Form.Control
            type="text"
            placeholder="Link a tu página de facebook"
            ref={(c) => (this.facebookUser = c)}
          />
        </Form.Group>
        <Form.Group controlId="formInstagram">
          <Form.Label>¿Cuál es tu página instagram? (Opcional)</Form.Label>
          <Form.Control
            type="text"
            placeholder="Link a tu página de instagram"
            ref={(c) => (this.instagramUser = c)}
          />
        </Form.Group>
        <Form.Group controlId="formType">
          <Form.Label>Categoría de tu negocio *</Form.Label>
          <Form.Control
            as="select"
            ref={(c) => (this.type = c)}
            onChange={() =>
              this.setState({ showOther: this.type.value == '20' })
            }
          >
            <option value="0">Selecionar</option>
            <option value="1">Arte y entretenimiento</option>
            <option value="2">Educación</option>
            <option value="3">Salud</option>
            <option value="4">Deportes</option>
            <option value="5">Finanzas</option>
            <option value="6">Mascotas</option>
            <option value="7">Tecnología</option>
            <option value="8">Compras</option>
            <option value="9">Automotriz</option>
            <option value="10">Universidades</option>
            <option value="11">Comidas y bebidas</option>
            <option value="12">Servicios legales</option>
            <option value="13">Viajes educativos o empresariales</option>
            <option value="14">Turismo</option>
            <option value="15">Comercio - Ventas de productos</option>
            <option value="16">Construcción y reparaciones</option>
            <option value="17">Familia y Hogar</option>
            <option value="18">Noticias e información</option>
            <option value="19">Belleza</option>
            <option value="20">Otro</option>
          </Form.Control>
        </Form.Group>
        {this.state.showOther ? (
          <Form.Group controlId="formOtherType">
            <Form.Label>¿Cuál? *</Form.Label>
            <Form.Control
              type="text"
              placeholder="Nombre de la categoría"
              ref={(c) => (this.otherType = c)}
            />
          </Form.Group>
        ) : null}

        <Form.Group controlId="formDescription">
          <Form.Label>Cuéntanos qué hace tu empresa *</Form.Label>
          <Form.Control
            as="textarea"
            rows="4"
            placeholder="Servicios que ofreces, cuánto llevas en el mercado, tu misión, visión, equipo de trabajo u objetivos, etc."
            ref={(c) => (this.description = c)}
          />
        </Form.Group>

        <Form.Group controlId="formProfilePhoto">
          <Form.Label>
            Adjunta una foto tuya para que sepan quién eres *
          </Form.Label>
          <Form.File
            custom
            label={this.state.profilePhotoName}
            accept="image/x-png,image/jpeg"
            multiple={false}
            data-browse="Buscar"
            lang="es"
            onChange={() => {
              const file = this.profilePicture.files[0];
              this.setState({
                profilePhotoName: file.name,
                profilePhotoFile: file,
              });
            }}
            ref={(c) => (this.profilePicture = c)}
          />
        </Form.Group>

        <Form.Group controlId="formLogo">
          <Form.Label>Si tienes un Logo ¡Puedes adjuntarlo!</Form.Label>
          <Form.File
            custom
            label={this.state.companyLogoName}
            accept="image/x-png,image/jpeg"
            multiple={false}
            data-browse="Buscar"
            lang="es"
            onChange={() => {
              const file = this.logoImage.files[0];
              this.setState({
                companyLogoName: file.name,
                companyLogoFile: file,
              });
            }}
            ref={(c) => (this.logoImage = c)}
          />
        </Form.Group>

        <Form.Group controlId="formTeamPhoto">
          <Form.Label>
            ¿Tienes un equipo de trabajo? ¡Queremos conocerlo! adjunta una foto
          </Form.Label>
          <Form.File
            custom
            label={this.state.teamPhotoName}
            accept="image/x-png,image/jpeg"
            multiple={false}
            data-browse="Buscar"
            lang="es"
            onChange={() => {
              const file = this.teamPicture.files[0];
              this.setState({
                teamPhotoName: file.name,
                teamPhotoFile: file,
              });
            }}
            ref={(c) => (this.teamPicture = c)}
          />
        </Form.Group>

        <Form.Group controlId="formProductPhotos">
          <Form.Label>
            Adjunta tres fotos de los productos o servicios que ofreces
          </Form.Label>
          <Form.File
            custom
            label={this.state.productPhotoName}
            accept="image/x-png,image/jpeg"
            multiple={true}
            data-browse="Buscar"
            lang="es"
            onChange={() => {
              const files = this.productPicture.files;
              this.setState({
                productPhotoName:
                  files[0].name + ', ' + (files[1] ? files[1].name : ''),
                productPhotoFiles: files,
              });
            }}
            ref={(c) => (this.productPicture = c)}
          />
        </Form.Group>
        <p className="mt-5 pt-3">
          *Una vez recibida tu información, nuestro equipo se encargará de
          retocar tus imágenes y crear tu espacio digital.
        </p>
        <p className="mb-5">
          *Una vez creado tu espacio, te enviaremos un mail de confirmación. Con
          esto podrás ingresar y administrar tu propio espacio.
        </p>

        <Form.Group controlId="formisActive">
          <Form.Check
            type="checkbox"
            label="Acepto términos y condiciones *"
            ref={(c) => (this.acceptTerms = c)}
          />
        </Form.Group>
        <hr />
        {this.buildError()}
        <div className="col-12 d-flex justify-content-center">
          <Spinner
            className={{
              'd-none': !this.state.loading,
              'd-block': this.state.loading,
            }}
            as="span"
            animation="border"
            size="md"
            role="status"
            aria-hidden="false"
          />
        </div>
        <Button
          className="col-12 mt-5"
          variant="primary"
          onClick={this.onSubmit.bind(this)}
          disabled={this.state.loading}
        >
          Registrarse
        </Button>
        <p className="text-center mt-4">
          ¿Ya tienes una cuenta? <Link to="login">¡Entra ahora!</Link>
        </p>
      </Form>
    );
  }

  buildModal() {
    return (
      <Modal
        centered
        show={this.state.showModal}
        onHide={this.closeModal.bind(this)}
        size="lg"
      >
        <Modal.Body className="bg-primary text-white">
          <Image
            src="assets/images/logo-white.png"
            className="modal-logo lazyload"
          />
          <Col className="text-center">
            <Image
              src="assets/images/message.png"
              className="message-logo mb-2 lazyload"
            />
            <h5 className="font-weight-bold">
              ¡ESTAMOS CREANDO TU ESPACIO EN LA WEB!
            </h5>
            <p>PRONTO TE ESTAREMOS CONFIRMANDO A TU CORREO ELECTRÓNICO</p>
          </Col>
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-center">
          <Button variant="primary" onClick={this.closeModal.bind(this)}>
            Entendido
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
  closeModal() {
    this.setState({ showModal: false, toHome: true });
  }

  buildError() {
    if (!this.state.error) {
      return null;
    }
    return <Alert variant="danger">{this.state.errorMessage}</Alert>;
  }
  async onSubmit(e) {
    this.setState({ loading: true });
    try {
      if (!this.validateFields()) {
        this.setState({ error: true, loading: false });
        return;
      }
      const profilePictureLink = await this.uploadPhoto(
        this.state.profilePhotoFile
      );
      const logoImageLink = await this.uploadPhoto(this.state.companyLogoFile);
      const teamPictureLink = await this.uploadPhoto(this.state.teamPhotoFile);
      const product1Link = await this.uploadPhoto(
        this.state.productPhotoFiles[0]
      );
      const product2Link = await this.uploadPhoto(
        this.state.productPhotoFiles[1]
      );
      const product3Link = await this.uploadPhoto(
        this.state.productPhotoFiles[2]
      );

      const geolocation = await GeocodeAPI.locationFromAddress(
        this.location.value
      );
      if (!geolocation) {
        geolocation = { lat: null, lng: null };
      }
      const company = {
        userName: this.userName.value,
        name: this.name.value,
        email: this.email.value,
        password: this.password.value,
        phoneNumber: this.phoneNumber.value,
        location: this.location.value,
        type: this.type.value,
        description: this.description.value,
        facebookUser: this.facebookUser.value,
        instagramUser: this.instagramUser.value,
        latitude: geolocation.lat,
        longitude: geolocation.lng,
        profilePictureLink: profilePictureLink,
        logoImageLink: logoImageLink,
        teamPictureLink: teamPictureLink,
        product1Link: product1Link,
        product2Link: product2Link,
        product3Link: product3Link,
      };
      if (this.state.showOther) {
        company.otherType = this.otherType.value;
      }
      const newCompany = await CompanyRepository.create(company);

      trackEvent('CompleteRegistration');
      gtag_report_conversion('AW-647239265/rGwGCKTils4BEOGs0LQC');
      this.setState({ showModal: true, loading: false });
    } catch (e) {
      alert(e.message);
      this.setState({
        loading: false,
        error: true,
        errormessage:
          'Ha ocurrido un error, por favor revisa que hayas llenado todos los campos obligatorios',
      });
    }
  }
  validateFields() {
    if (this.userName.value.length < 3) {
      this.setState({
        errorMessage: 'Debes ingresar un nombre válido',
      });
      return false;
    }

    if (this.name.value.length < 3) {
      this.setState({
        errorMessage: 'Debes ingresar un nombre de negocio válido',
      });
      return false;
    }

    if (this.state.showOther && this.otherType.value.length < 3) {
      this.setState({
        errorMessage: 'Debes escribir una categoría de negocio valida',
      });
      return false;
    }
    if (
      this.email.value.length < 5 ||
      !this.email.value.includes('@') ||
      !this.email.value.includes('.')
    ) {
      this.setState({
        errorMessage: 'Introduce un email válido',
      });
      return false;
    }
    if (this.password.value.length < 6) {
      this.setState({
        errorMessage: 'La contraseña debe tener 6 caracteres o más',
      });
      return false;
    }
    if (this.phoneNumber.value.length < 10) {
      this.setState({
        errorMessage: 'Debes ingresar un número de celular válido',
      });
      return false;
    }
    if (this.location.value.length < 3) {
      this.setState({
        errorMessage: 'Debes ingresar una ubicación válida',
      });
      return false;
    }
    if (this.type.value == 0) {
      this.setState({
        errorMessage: 'Debes seleccionar una categoría de negocio',
      });
      return false;
    }
    if (this.state.showOther && this.otherType.value.length < 3) {
      this.setState({
        errorMessage: 'Debes escribir una categoría de negocio valida',
      });
      return false;
    }
    if (this.description.value.length < 3) {
      this.setState({
        errorMessage: 'Debes ingresar una descripción válida',
      });
      return false;
    }
    if (!this.state.profilePhotoFile) {
      this.setState({ errorMessage: 'Debes adjuntar una foto tuya' });
      return false;
    }
    if (this.acceptTerms.checked != true) {
      this.setState({
        errorMessage: 'Debes aceptar los términos y condiciones para continuar',
      });
      return false;
    }
    return true;
  }

  async uploadPhoto(file) {
    if (!file) {
      return null;
    }
    const snapshot = await firebase
      .storage()
      .ref(`/PHOTOS/${new Date().getTime() + file.name}`)
      .put(file);
    return snapshot.ref.getDownloadURL();
  }
}

export default SignUpScreen;
