import './bottom-bar.scss';
import React from 'react';
import { Image } from 'react-bootstrap';
class ContactInfo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="row mt-5 text-primary text-center mb-3 d-none d-lg-flex">
        <div className="col-6 col-lg-4 text-center text-lg-right pt-1">
          <a target="blank" href="https://www.facebook.com/PYPPERCOL">
            <Image
              src={
                this.props.secondLevel
                  ? '../assets/images/facebook-light.jpg'
                  : 'assets/images/facebook-light.jpg'
              }
              className="icon-szie mr-3"
            />
          </a>
          <a target="blank" href="https://www.instagram.com/pypper_col/">
            <Image
              src={
                this.props.secondLevel
                  ? '../assets/images/insta-light.jpg'
                  : 'assets/images/insta-light.jpg'
              }
              className="icon-szie"
            />
          </a>
        </div>
        <div className="col-6 col-lg-4 text-center text-lg-center">
          <Image
            src={
              this.props.secondLevel
                ? '../assets/images/logo-white-border.png'
                : 'assets/images/logo-white-border.png'
            }
            className="logo-szie"
          />
        </div>
        <div className="col-12 col-lg-4 mt-4 mt-lg-0 text-center text-lg-left">
          <h4>@pypper.com</h4>
        </div>
      </div>
    );
  }
}

export default ContactInfo;
