import './app-bar.scss';
import React from 'react';
import { Navbar, Nav, Button, Image } from 'react-bootstrap';
import { Link, Redirect } from 'react-router-dom';
import { AuthModel } from '../models';
class AppBar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      toggle: false,
      isLoggedIn: AuthModel.isAuthenticated,
      accountType: AuthModel.currentAccount.type,
    };
  }
  render() {
    const buttonText = this.state.isLoggedIn
      ? 'Cerrar sesión'
      : 'Iniciar sesión';
    return (
      <Navbar
        bg={this.props.white ? 'white' : 'transparent'}
        expand="lg"
        fixed={this.props.unfixed ? null : 'top'}
        onToggle={(value) => this.setState({ toggle: value })}
        className={{
          'd-lg-none': this.props.hideOnDesktop,
          'd-lg-block': this.props.hideOnMobile,
          'd-none': this.props.hideOnMobile,
        }}
      >
        <Navbar.Toggle
          className={{
            'toggler-light': this.state.toggle || this.props.dark,
            'toggler-dark': !this.state.toggle && !this.props.dark,
          }}
          aria-controls="basic-navbar-nav"
        />
        <div className="mr-auto"></div>
        <Link to="/">
          <Navbar.Brand>
            <Image
              src={
                this.props.secondLevel
                  ? '../assets/images/logo-white-border.png'
                  : 'assets/images/logo-white-border.png'
              }
              className="logo-szie"
            />
          </Navbar.Brand>
        </Link>
        <div className="mr-auto"></div>
        <Navbar.Collapse
          id="basic-navbar-nav"
          className={{ 'menu-unfixed': this.props.unfixed || this.props.white }}
        >
          <div className="mr-auto"></div>
          <div className="d-lg-none menu-responsive-bg bg-primary">
            <div className="menu-responsive-bottom" />
          </div>
          <Nav className="text-light">
            <Nav.Link className="mr-3">
              <Link to="/">Home</Link>
            </Nav.Link>
            <Nav.Link className="mr-3">
              <Link to="/about">Nosotros</Link>
            </Nav.Link>
            <Nav.Link className="mr-3">
              <Link to="/team">Equipo</Link>
            </Nav.Link>
            <Nav.Link className="mr-3">
              <Link to="/join">¡Quiero estar en Pypper!</Link>
            </Nav.Link>
            <Nav.Link className="mr-3">
              <Link to="/blog">Blog</Link>
            </Nav.Link>
            {this.state.isLoggedIn && this.state.accountType == 2 ? (
              <Nav.Link className="mr-3">
                <Link to="/account">Mi cuenta</Link>
              </Nav.Link>
            ) : null}
            <Nav.Link
              onClick={() => {
                if (this.state.isLoggedIn) {
                  AuthModel.isAuthenticated = false;
                  localStorage.removeItem('authentication_token');
                }
              }}
            >
              <Link to="/login">
                <Button variant="primary" className="d-none d-lg-block">
                  {buttonText}
                </Button>
                <Button variant="light" className="d-lg-none">
                  {buttonText}
                </Button>
              </Link>
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}

export default AppBar;
