import './create-offer.scss';
import React from 'react';
import {
  Row,
  Col,
  Container,
  Image,
  Card,
  Button,
  Form,
  Alert,
  Modal,
  Spinner,
} from 'react-bootstrap';
import { AppBar, SearchCard, BottomBar } from '../components';
import { Link, Redirect } from 'react-router-dom';
import { CompanyRepository, OfferRepository } from '../repositories';
import firebase from 'firebase';
import { trackEvent } from '../common/api/facebook-pixel';
import { gtag_report_conversion } from '../common/api/google-adds';
import { AuthModel } from '../models';

class CreateOfferScreen extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      error: false,
      loading: false,
      errorMessage:
        'Ha ocurrido un error, por favor revisa que hayas llenado todos los campos obligatorios',
      profilePhotoName: 'Seleccionar archivo',
      companyLogoName: 'Seleccionar archivo',
      teamPhotoName: 'Seleccionar archivo',
      showModal: false,
      toAccount: false,
      company: { name: '', description: '', phoneNumber: '', email: '' },
    };
  }
  componentDidMount() {
    this.fetchCompany();
  }
  render() {
    if (this.state.toAccount) {
      return <Redirect to="/account" />;
    }
    return (
      <Container fluid>
        <AppBar white />
        <Row className="mt-5 mt-lg-0">
          <Col
            lg={{ span: 3 }}
            className="create-offer-bg position-relative p-0 d-flex align-items-end"
          >
            <Image
              className="half-clip d-lg-none"
              src={`assets/images/create-offer.png`}
            />
            <h3 className="title-white text-white company-title text-uppercase">
              {this.state.company.name}
            </h3>
            <div className="bg-primary text-right text-white text-box team-description d-none d-lg-block">
              Cuando llenes el formulario, tu información será enviada y
              validada por nosotros
            </div>
            {this.buildSocialNetworkButtons()}
          </Col>
          <Col
            className="create-offer-content p-0 pt-5 form-container text-lg-left text-center"
            lg={{ span: 9 }}
          >
            <Col className="mt-lg-5 mb-5 mb-lg-0" lg={{ offset: 2, span: 10 }}>
              <div className="col-12 text-lg-right w-100 pr-lg-5 mb-4 mb-lg-2">
                <Link to="/account">
                  <Button variant="outline-primary">
                    <Image
                      className="button-icon-left"
                      src="assets/images/go-back-icon.png"
                    />
                    Volver
                  </Button>
                </Link>
              </div>
              <Image
                className="company-logo mb-4"
                src={this.state.company.logoImageLink}
              />
              <Row className="text-center">
                <h1 className="title mb-5 col">¡CREA TU OFERTA!</h1>
              </Row>
              {this.buildForm()}
            </Col>
          </Col>
        </Row>
        {this.buildModal()}

        {this.buildChatIcon()}
        {this.buildChat()}
        {this.buildViewCounter()}
      </Container>
    );
  }

  buildViewCounter() {
    return (
      <div className="text-white bg-primary view-counter">
        {this.state.company.views}
        <Image className="lazyload" src="assets/images/eye-icon.png" />
      </div>
    );
  }
  buildChatIcon() {
    return (
      <div
        className="bg-white chat-icon"
        onClick={() => this.setState({ showChat: !this.state.showChat })}
      >
        <Image className="lazyload" src="assets/images/chat-icon.png" />
      </div>
    );
  }

  buildChat() {
    if (!this.state.showChat) {
      return null;
    }
    return (
      <div className="chat p-3">
        <Form>
          <div className="col-12 text-center mb-3">
            <Image src="assets/images/chat-user.png" />
          </div>
          <Form.Group controlId="formName">
            <Form.Control
              type="text"
              placeholder="Nombre completo"
              ref={(c) => (this.questionName = c)}
            />
          </Form.Group>
          <Form.Group controlId="formEmail">
            <Form.Control
              type="text"
              placeholder="Tu correo electrónico"
              ref={(c) => (this.questionEmail = c)}
            />
          </Form.Group>
          <Form.Group controlId="formDescription">
            <Form.Control
              as="textarea"
              placeholder="¿Tienes alguna duda?"
              rows="3"
              ref={(c) => (this.questionDescription = c)}
            />
          </Form.Group>
          <div className="col-12 text-center">
            <Button onClick={this.sendMessage.bind(this)}>Enviar</Button>
          </div>
        </Form>
      </div>
    );
  }
  sendMessage() {
    if (
      this.questionName.value.length < 3 ||
      this.questionEmail.value.length < 3 ||
      this.questionDescription.value < 3
    ) {
      alert(
        'Por favor completa los datos de la pregunta para que podamos ayudarte.'
      );
      return;
    }
    const question = {
      name: this.questionName.value,
      email: this.questionEmail.value,
      description: this.questionDescription.value,
    };
    CompanyRepository.sendQuestion(question)
      .then(() => alert('Tu pregunta ha sido enviada con éxito'))
      .catch(() =>
        alert('Lo sentimos, ha ocurrido un error al enviar tu pregunta')
      )
      .finally(() => this.setState({ showChat: false }));
  }
  buildForm() {
    return (
      <Form className="bg-light-gray mb-5 text-left pt-5 pb-5">
        <div className="col-lg-8 offset-lg-2">
          <Row>
            <Col lg={{ span: 6, order: 1 }}>
              <Form.Group controlId="formDescription">
                <Form.Label>Breve descripción de la oferta *</Form.Label>
                <Form.Control
                  as="textarea"
                  rows="8"
                  ref={(c) => (this.description = c)}
                />
              </Form.Group>
            </Col>
            <Col lg={{ span: 6, order: 0 }}>
              <Form.Group controlId="formProduct">
                <Form.Label>Servicio o producto *</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Servicio o producto"
                  ref={(c) => (this.product = c)}
                />
              </Form.Group>

              <Form.Group controlId="formType">
                <Form.Label>Categoría *</Form.Label>
                <Form.Control as="select" ref={(c) => (this.type = c)}>
                  <option value="0"></option>
                  <option value="1">Arte y entretenimiento</option>
                  <option value="2">Educación</option>
                  <option value="3">Salud</option>
                  <option value="4">Deportes</option>
                  <option value="5">Finanzas</option>
                  <option value="6">Mascotas</option>
                  <option value="7">Tecnología</option>
                  <option value="8">Compras</option>
                  <option value="9">Automotriz</option>
                  <option value="10">Universidades</option>
                  <option value="11">Comidas y bebidas</option>
                  <option value="12">Servicios legales</option>
                  <option value="13">Viajes educativos o empresariales</option>
                  <option value="14">Turismo</option>
                  <option value="15">Comercio - Ventas de productos</option>
                  <option value="16">Construcción y reparaciones</option>
                  <option value="17">Familia y Hogar</option>
                  <option value="18">Noticias e información</option>
                  <option value="19">Belleza</option>
                  <option value="20">Otro</option>
                </Form.Control>
              </Form.Group>

              <Form.Group controlId="formCount">
                <Form.Label>Cantidad de descuento *</Form.Label>
                <Form.Control
                  max="100"
                  min="0"
                  type="number"
                  placeholder="Cantidad de descuento"
                  ref={(c) => (this.discountRate = c)}
                />
              </Form.Group>

              {this.buildError()}
              <div className="col-12 d-flex justify-content-center">
                <Spinner
                  className={{
                    'd-none': !this.state.loading,
                    'd-block': this.state.loading,
                  }}
                  as="span"
                  animation="border"
                  size="md"
                  role="status"
                  aria-hidden="false"
                />
              </div>
              <Button
                className="col-12 mt-5"
                variant="primary"
                onClick={this.onSubmit.bind(this)}
                disabled={this.state.loading}
              >
                Enviar
              </Button>
            </Col>
          </Row>
        </div>
      </Form>
    );
  }

  buildSocialNetworkButtons() {
    return (
      <div className="social-buttons bg-primary p-2 d-none d-lg-block">
        <a target="blank" href="https://www.facebook.com/PYPPERCOL">
          <Image
            className="social-button lazyload"
            src="assets/images/facebook.png"
          />
        </a>
        <a target="blank" href="https://www.instagram.com/pypper_col/">
          <Image
            className="social-button lazyload"
            src="assets/images/instagram.png"
          />
        </a>
      </div>
    );
  }

  buildModal() {
    return (
      <Modal
        centered
        show={this.state.showModal}
        onHide={this.closeModal.bind(this)}
        size="lg"
      >
        <Modal.Body className="bg-primary text-white">
          <Image
            src="assets/images/logo-white.png"
            className="modal-logo lazyload"
          />
          <Col className="text-center">
            <Image
              src="assets/images/quotation.png"
              className="message-logo mb-2 lazyload"
            />
            <h5 className="font-weight-bold">¡ESTAMOS CREANDO TU OFERTA!</h5>
          </Col>
        </Modal.Body>
        <Modal.Footer className="d-flex justify-content-center">
          <Button variant="primary" onClick={this.closeModal.bind(this)}>
            Entendido
          </Button>
        </Modal.Footer>
      </Modal>
    );
  }
  closeModal() {
    this.setState({ showModal: false, toAccount: true });
  }

  buildError() {
    if (!this.state.error) {
      return null;
    }
    return <Alert variant="danger">{this.state.errorMessage}</Alert>;
  }
  async onSubmit(e) {
    this.setState({ loading: true });
    try {
      if (!this.validateFields()) {
        this.setState({ error: true, loading: false });
        return;
      }
      const offer = {
        company: this.state.company.id,
        product: this.product.value,
        type: this.type.value,
        discountRate: this.discountRate.value,
        description: this.description.value,
      };
      await OfferRepository.create(offer);

      this.setState({ showModal: true, loading: false });
    } catch (e) {
      this.setState({
        loading: false,
        error: true,
        errormessage:
          'Ha ocurrido un error, por favor revisa que hayas llenado todos los campos obligatorios',
      });
    }
  }
  validateFields() {
    if (this.product.value.length < 3) {
      this.setState({
        errorMessage: 'Introduce un producto válido',
      });
      return false;
    }
    if (this.type.value == 0) {
      this.setState({
        errorMessage: 'selecciona una categoría válida',
      });
      return false;
    }
    if (this.discountRate.value < 1) {
      this.setState({
        errorMessage: 'Ingresa una cantidad válida',
      });
      return false;
    }

    if (this.description.value.length < 10) {
      this.setState({
        errorMessage: 'Introduce una descripción de mínimo 10 caracteres',
      });
      return false;
    }
    return true;
  }
  fetchCompany() {
    CompanyRepository.findByAccount(AuthModel.currentAccount.id)
      .then((result) =>
        result.data[0] ? this.setState({ company: result.data[0] }) : null
      )
      .catch((e) => {});
  }
}

export default CreateOfferScreen;
