import React from 'react';
import { Row, Col, Image } from 'react-bootstrap';

class LeftText extends React.Component {
  render() {
    return (
      <Row className={this.props.className + ' blue-lines'}>
        <Col lg={{ span: 6, order: 1 }}>
          <Row className="p-0 middle-blue bg-primary">
            <div className="col-1 middle-white bg-white h-100" />
          </Row>
          <Row className="p-0 bg-primary">
            {this.props.isVideo ? (
              <iframe
                className="video-responsive"
                src={this.props.headImage}
                frameborder="0"
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowfullscreen
              ></iframe>
            ) : (
              <Image className="full-img lazyload" src={this.props.headImage} />
            )}
          </Row>
          <Row className="full-blue bg-primary">
            <div className="col-1 bg-white h-100" />
            <div className="col-5 d-flex align-items-center justify-content-center">
              <Image
                className="w-25 lazyload"
                src="assets/images/white-icon.png"
              />
            </div>
            <div className="col-6 p-0">
              <Image
                className="close-img lazyload"
                src={this.props.footerImage}
              />
            </div>
          </Row>
        </Col>
        <Col
          className="col-12 text-center text-lg-left"
          lg={{ span: 4, offset: 2, order: 0 }}
        >
          <div className="pl-4 pr-4 pl-lg-0 pr-lg-0">
            <h1 className="title mt-5 mb-4 text-uppercase">
              {this.props.title}
            </h1>
            <p className="description pb-5 pr-lg-4">{this.props.description}</p>
          </div>
        </Col>
      </Row>
    );
  }
}

export default LeftText;
